import React from 'react';
import {parseDayJsFrom} from "../todoist/DueDate";
import {clock} from "../domain/Clock";
import {classNameIf, classNames} from "../shared/components/className";
import styles from "./ScheduleDayView.module.css"
import {DaySchedule} from "./ScheduleWeekView";
import {usePlanParam} from "./usePlanParam";
import usePlanNavigation from "./planNavigate";
import PriorityView from "./PriorityView";

interface ScheduleDayViewProps {
    schedule: DaySchedule
}

function ScheduleDayView({schedule}: ScheduleDayViewProps) {
    const {day} = usePlanParam();
    const {planFor} = usePlanNavigation();

    const today = clock.today();
    const isToday = today === schedule.day
    const isOverdue = today > schedule.day
    const current = day === schedule.day

    function performNavigate() {
        planFor(schedule.day)
    }

    return <div className={classNames(
        styles.ScheduleDayView,
        classNameIf(current, styles.Current))}
                onClick={performNavigate}
    >
        <span className={classNames(
            styles.Day,
            classNameIf(isToday, styles.Today),
            classNameIf(isOverdue, styles.Overdue)
        )}>{parseDayJsFrom(schedule.day).date()}</span>

        <div className={styles.Priorities}>
            {schedule.tasks.map(({priority}, index) => <PriorityView key={index} value={priority}/>)}
        </div>
    </div>;
}

export default ScheduleDayView;
