import React from 'react';
import styles from "./EstimationChange.module.css"
import {HotKey} from "../shared/hotkeys/HotKey";
import {useStoreActions, useStoreState} from "../store";
import {TaskId} from "../shared/TaskId";
import SubActions, {Shortcut} from "../shared/components/sub-actions/SubActions";
import SubAction from "../shared/components/sub-actions/SubAction";
import Icon from "../shared/Icon";

interface EstimationChangeProps {
    taskId: TaskId
}

function EstimationChange({taskId}: EstimationChangeProps) {
    let {
        timeLabels,
    } = useStoreState(state => state);

    const {assignEstimation} = useStoreActions(actions => actions);

    return <div className={styles.EstimationChange}>
        <SubActions shortcut="E" label={<Icon name="stopwatch.svg"/>}>
            {timeLabels.map((label, index) => {
                    let shortcutKey = `${index + 1}`
                    return <SubAction key={shortcutKey}
                                   shortcut={`${shortcutKey}` as Shortcut}
                                   label={label.name}
                                   color={label.color.value}
                                   onClick={() => {
                                       assignEstimation({taskId: taskId, label: label.name})
                                   }}/>
                }
            )}

        </SubActions>
    </div>;
}

export default EstimationChange;
