import React from "react";

const labelsForKeys: Record<string, string> = {
    "enter": "",
    "shift": "",
    "comma": "<",
    "period": ">",
    "meta": "",
    "backspace": "",
    "alt": "",
    "up": "",
    "down": "",
    "left": "",
    "right": ""
}

export function shortcutFrom(label: string | React.ReactNode) {
    if (typeof label === "string") {
        return label.split("+").map(it => it.trim()).map(element => labelsForKeys[element] || element).join(" + ")
    }
    return label
}
