import TaskItem from "./TaskItem";
import {DailyTask} from "./DailyTask";
import {TaskId} from "../shared/TaskId";

interface DailyTasksProps {
    tasks: DailyTask[];
    onSelectTask: (taskId: TaskId) => void;
    reflectDuration: boolean;
}

export function DailyTasks({tasks, onSelectTask, reflectDuration}: DailyTasksProps) {

    return <>
        {tasks.map((task) =>
            <TaskItem  key={task.id} task={task} onSelectTask={onSelectTask} reflectDuration={reflectDuration}/>
        )}
    </>
}
