import React from 'react';
import {Hex} from "../../Color";
import styles from "./ColorItem.module.css"
import Icon from "../../Icon";
import {classNameIf, classNames} from "../className";

interface ColorItemProps {
    value?: Hex,
    onClick?: (value: Hex | undefined) => void
}

function ColorItem({value, onClick = noop}: ColorItemProps) {
    return <div
        className={classNames(styles.ColorItem, classNameIf(!value, styles.None))}
        style={{backgroundColor: value, borderColor: value}}
        onClick={() => onClick(value)}
    >
        {!value &&< Icon name="x.svg"/>}
    </div>;
}

const noop = (value: Hex | undefined) => {
}

export default ColorItem;
