import {Priority} from "./Priority";
import {Hex} from "./Color";

export function priorityColor(priority: Priority): Hex {
    switch (priority) {
        case Priority.HIGH:
            return "#d1453b"
        case Priority.MEDIUM:
            return "#eb8909";
        case Priority.LOW:
            return "#246fe0";
        default:
            return "#666"
    }
}
