import {ColorKey, getColorByKey, Hex} from "../shared/Color";
import styles from "./ProjectSummary.module.css"
import range from "../shared/range";

interface ItemProps {
    color?: Hex;
}

function Item({color}: ItemProps) {
    return <div  className={styles.Summary}
                 style={{borderColor: color}}/>
}

interface ProgressProps {
    value: number
}

function color(value: number): Hex | undefined {

    if (value < 4) {
        return undefined
    }
    if (value < 8) {
        return getColorByKey(ColorKey.YELLOW)
    }
    if (value < 12) {
        return getColorByKey(ColorKey.ORANGE)
    }
    return getColorByKey(ColorKey.RED)
}



function ProjectSummary({value}: ProgressProps) {

    return <div className={styles.Summaries}>
        {range(value).map(v => (<Item key={v} color={color(v)}/>))}
    </div>


}




export default ProjectSummary
