import React, {useState} from 'react';
import {HotKey} from "./HotKey";
import {ColorKey, getColorByKey, Hex} from "../Color";
import styles from "./HotkeyConfirmation.module.css"
import Icon from "../Icon";
import {useHotkeysContext} from "./useHotkeysContext";

interface ConfirmationProps {
    action: () => void;
    theme?: "default" | "danger"
    binding: string,
    label: string | React.ReactNode,
    shortcut?: string | React.ReactNode,
    color?: Hex,
    disabled?: boolean
    loading?: boolean
}

const HotkeyConfirmation = ({
                                action,
                                binding,
                                shortcut = binding,
                                theme = "default",
                                label,
                                loading,
                                color,
                                disabled
                            }: ConfirmationProps) => {
    const [confirming, setConfirming] = useState(false);

    const {enableDefaultScopes, enableOnlyScope} = useHotkeysContext();

    const handleButtonClick = () => {
        enableOnlyScope("confirmation")
        setConfirming(true);
    };

    const handleConfirm = () => {
        action();
        setConfirming(false);
        enableDefaultScopes()
    };

    const handleCancel = () => {
        setConfirming(false);
        enableDefaultScopes();
    };

    return (
        <span className={styles.Container}>
            {confirming ? (
                <>
                    <div className={styles.Overlay}/>
                    <div className={styles.Confirmations}>
                        <HotKey binding="enter"
                                label={<Icon name="check.svg"/>}
                                color={theme === "danger" ? getColorByKey(ColorKey.RED) : getColorByKey(ColorKey.GREEN)}
                                glow
                                onClick={handleConfirm}
                                scope="confirmation"/>
                        <HotKey binding="esc"
                                glow
                                label={<Icon name="ban.svg"/>}
                                onClick={handleCancel}
                                scope="confirmation"/>
                    </div>
                </>

            ) : (
                <HotKey binding={binding} shortcut={shortcut} label={label} loading={loading} color={color}
                        disabled={disabled}
                        onClick={handleButtonClick}/>
            )}
        </span>
    );
};

export default HotkeyConfirmation;


