import React from 'react';
import {TaskId} from "../shared/TaskId";
import {useStoreActions} from "../store";
import {period, Period} from "../shared/Period";
import Icon from "../shared/Icon";
import {ColorKey, getColorByKey, Hex} from "../shared/Color";
import {Hexes} from "../shared/hotkeys/hexes";
import styles from "./ScheduleTask.module.css"
import dayjs from "../shared/dayjs";
import SubActions, {Shortcut} from "../shared/components/sub-actions/SubActions";
import SubAction from "../shared/components/sub-actions/SubAction";

interface ScheduleTaskProps {
    taskId: TaskId;
    enabled: boolean;
}

function dayName(period: Period): string {
    return dayjs().add(period.amount, period.unit).format("ddd")
}

const options: { period: Period, label: string, color?: Hex, withDayName?: boolean, shortcut?: Shortcut }[] = [
    {
        label: "Today",
        period: period(0, "days"),
        color: getColorByKey(ColorKey.BERRY_RED)

    },
    {
        label: "Tomorrow",
        period: period(1, "day"),
        color: getColorByKey(ColorKey.RED)
    },
    {
        label: "2d",
        period: period(2, "days"),
        color: getColorByKey(ColorKey.ORANGE),
        withDayName: true
    },
    {
        label: "3d",
        period: period(3, "days"),
        color: getColorByKey(ColorKey.YELLOW),
        withDayName: true
    },
    {
        label: "4d",
        period: period(4, "days"),
        color: getColorByKey(ColorKey.GREEN),
        withDayName: true
    },
    {
        label: "5d",
        period: period(5, "days"),
        color: getColorByKey(ColorKey.LIME_GREEN),
        withDayName: true
    },

    {
        shortcut: "shift + 1",
        label: "1w",
        period: period(1, "week"),
        color: getColorByKey(ColorKey.SKY_BLUE),
        withDayName: true
    }, {
        shortcut: "shift + 2",
        label: "2w",
        period: period(2, "weeks"),
        color: getColorByKey(ColorKey.LIGHT_BLUE),
        withDayName: true
    },
    {
        shortcut: "a",
        label: "1m",
        period: period(1, "month"),
        withDayName: true
    }
]

function ScheduleTask({taskId, enabled}: ScheduleTaskProps) {

    const {rescheduleTask2, unscheduleTask} = useStoreActions(actions => actions)

    const performReschedule = (period: Period) => {
        rescheduleTask2({taskId, period})
    }
    const performUnschedule = () => {
        unscheduleTask({taskId})
    }
    return <div className={styles.ScheduleTask}>
        <SubActions shortcut="S" label={<Icon name="calendar-days.svg"/>} disabled={!enabled}>
            <SubAction
                key="unschedule"
                shortcut="0"
                color={Hexes.UNSHEDULE}
                disabled={!enabled}
                label={<Icon name="ban.svg" size={12}/>}
                onClick={performUnschedule}
            />
            {options.map(({label, shortcut, period, color, withDayName}, index) => {
                const actionLabel = withDayName ? `${label} (${dayName(period)})` : label;
                return <SubAction key={index}
                                  shortcut={shortcut ? shortcut : `${index + 1}` as Shortcut}
                                  label={actionLabel}
                                  color={color}
                                  disabled={!enabled}
                                  onClick={() => {
                                      performReschedule(period);
                                  }}/>
            })
            }

        </SubActions>

    </div>;
}

export default ScheduleTask;
