// eslint-disable-next-line no-extend-native
Array.prototype.moveLeft = function <T>(this: T[], element: T): T[] {
    const index = this.indexOf(element);

    if (index === -1) {
        return this;
    }

    if (index > 0) {
        [this[index], this[index - 1]] = [this[index - 1], this[index]];
    }
    return this;
};

// eslint-disable-next-line no-extend-native
Array.prototype.moveRight = function <T>(this: T[], element: T): T[] {
    const index = this.indexOf(element);

    if (index === -1) {
        return this;
    }

    if (index < this.length - 1) {
        [this[index], this[index + 1]] = [this[index + 1], this[index]];
    }
    return this;
};

// eslint-disable-next-line no-extend-native
Array.prototype.moveFirst = function <T>(this: T[], element: T): T[] {
    const index = this.indexOf(element);

    if (index === -1) {
        return this;
    }

    if (index > 0) {
        this.splice(index, 1);
        this.unshift(element);
    }
    return this;
};

// eslint-disable-next-line no-extend-native
Array.prototype.moveLast = function <T>(this: T[], element: T): T[] {
    const index = this.indexOf(element);

    if (index === -1) {
        return this;
    }

    if (index < this.length - 1) {
        this.splice(index, 1);
        this.push(element);
    }
    return this;
};

// eslint-disable-next-line no-extend-native
Array.prototype.next = function <T>(element: T): T | undefined {
    const index = this.indexOf(element);
    if (index === -1 || index >= this.length - 1) return undefined;
    return this[index + 1];
}

// eslint-disable-next-line no-extend-native
Array.prototype.previous = function <T>(element: T): T | undefined {
    const index = this.indexOf(element);
    if (index === -1 || index === 0) return undefined;
    return this[index - 1];
}

// eslint-disable-next-line no-extend-native
Array.prototype.first = function <T>(): T | undefined {
    return this.length > 0 ? this[0] : undefined;
}

// eslint-disable-next-line no-extend-native
Array.prototype.last = function <T>(): T | undefined {
    return this.length > 0 ? this[this.length - 1] : undefined;
}

