import {HttpResponse} from "../../todoist/domain/HttpResponse";
import {HttpStatus} from "../../todoist/domain/HttpStatus";

export async function parseResponse<Res = undefined>(response: Response): Promise<HttpResponse<Res>> {
    return {
        status: response.status as HttpStatus,
        successful: response.ok,
        body: await tryParseJson<Res>(response.text())
    }
}

// @ts-ignore
async function tryParseJson<T = undefined>(body: Promise<string>): Promise<T> {
    let value = await body;
    if (value) {
        try {
            return JSON.parse(value)
        } catch (e) {
            // @ts-ignore
            return value;
        }
    }
}
