import React, {useEffect, useState} from 'react';
import ColorItem from "./ColorItem";
import {colors as themeColors, Hex} from "../../Color";
import Expandable from "../../../layout/Expandable";
import styles from "./ColorPicker.module.css"
import {Optional} from "../../Optional";


const colors: Optional<Hex>[] = [undefined, ...themeColors.map(it => it.value)];

interface ColorPickerProps {
    color?: Hex;
    availableColors?: Optional<Hex>[];
    onColorChange: (color: Optional<Hex>) => void
}

const ColorPicker = ({color, onColorChange, availableColors = colors}: ColorPickerProps) => {
    const [selectedColor, setSelectedColor] = useState<Hex | undefined>(color);

    useEffect(() => {
        onColorChange(selectedColor)
    }, [selectedColor]);

    return (
        <>
            <Expandable component={<ColorItem value={selectedColor}/>}>
                <div className={styles.Colors}>
                    {availableColors.map((color) => (
                        <ColorItem key={color} value={color} onClick={setSelectedColor}/>
                    ))}
                </div>
            </Expandable>
        </>
    );
};

export default ColorPicker;
