import React from 'react';
import Layout from "./layout/Layout";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import SettingsPage from "./settings/SettingsPage";
import Initialize from "./init/Initialize";
import EstimatePage from "./estimate/EstimatePage";
import WorkPage from "./work/WorkPage";
import PlanPage from "./plan/PlanPage";
import ProjectsPage from "./projects/ProjectsPage";
import ProjectPage from "./projects/ProjectPage";
import ProfilePage from "./settings/ProfilePage";
import AddProfilePage from "./settings/AddProfilePage";
import AppLibs from "./AppLibs";
import RedirectTo from "./support/RedirectTo";


function App() {

    return (
        <>
            <AppLibs>
                <BrowserRouter>
                    <Routes>
                        <Route index element={<Initialize/>}/>
                        <Route element={<Layout/>}>
                            <Route path="/estimate" element={<EstimatePage/>}/>
                            <Route path="/work" element={<WorkPage/>}/>
                            <Route path="/plan/:day" element={<PlanPage/>}/>
                            <Route path="/projects" element={<ProjectsPage/>}/>
                            <Route path="/projects/:id" element={<ProjectPage/>}/>
                            <Route path="/settings" element={<SettingsPage/>}/>
                            <Route path="/settings/profiles" element={<ProfilePage/>}/>
                            <Route path="/settings/profiles/add" element={<AddProfilePage/>}/>
                            <Route path="*" element={<RedirectTo path="/"/> } />
                        </Route>
                    </Routes>
                </BrowserRouter>
            </AppLibs>
        </>
    );
}


export default App;
