import React from 'react';
import Icon from "../shared/Icon";
import styles from "./Syncing.module.css"
import {useStoreState} from "../store";

interface SyncingProps {
    visible?: boolean
}

function Syncing({visible}: SyncingProps) {
    const syncing = useStoreState(state => state.syncing).length > 0 || visible;
    return <>{syncing && <div className={styles.Syncing}><Icon size={32} name="rotate.svg"/></div>}</>
}

export default Syncing;
