import {Hex} from "../shared/Color";
import {Minutes} from "../shared/Minutes";
import {UTCDayTime} from "../shared/UTCDayTime";
import {useEffect, useState} from "react";
import Stopwatch from "./Stopwatch";
import dayjs from "../shared/dayjs";
import useIsMobile from "../shared/components/useMinSize";

interface TimerProps {
    color: Hex,
    duration: Minutes,
    loading: boolean,
    startedAt?: UTCDayTime,
}


function Timer({duration, startedAt, color, loading}: TimerProps) {
    const [elapsedTime, setElapsedTime] = useState<number>(0);

    function updateTimer() {
        if (startedAt) {
            const seconds = dayjs().diff(dayjs(startedAt), "seconds");
            setElapsedTime(seconds);
        } else {
            setElapsedTime(0)
        }
    }

    const isMobile = useIsMobile();


    useEffect(() => {
        updateTimer();
        const intervalId = setInterval(() => {
            updateTimer();
        }, 1000);

        return () => clearInterval(intervalId);
    }, [startedAt]);
    return <Stopwatch duration={duration * 60} started={!!startedAt} color={color} elapsedTime={elapsedTime}
                      loading={loading}
                      size={isMobile ? 256 : 512} strokeWidth={isMobile ? 32 : 96}/>
}

export default Timer
