import React, {useEffect, useState} from "react";
import styles from "./Indicator.module.css";
import {ColorKey, getColorByKey, Hex} from "../Color";
import {classNameIf} from "./className";
import {useStoreState} from "../../store";
import {IndicatorStore} from "../../IndicatorStore";

export enum Area {
    ESTIMATION = "estimation",
    WORK = "work",
    NONE = "none"
}

export enum Severity {
    URGENT,
    INFO,
    WARN,
    BASIC
}

export interface IndicatorProps {
    area: Area;
    visible: boolean;
    indicator?: IndicatorStore;
    children: React.ReactNode;
}


const colors: Record<Severity, Hex> = {
    [Severity.BASIC]: getColorByKey(ColorKey.GREEN),
    [Severity.INFO]: getColorByKey(ColorKey.BLUE),
    [Severity.WARN]: getColorByKey(ColorKey.ORANGE),
    [Severity.URGENT]: getColorByKey(ColorKey.RED),

}

function Indicator({ area, visible, indicator: indicatorProps, children}: IndicatorProps) {

    const [color, setColor] = useState<Hex>();
    const indicator = useStoreState(state => state.indicator) || indicatorProps;

    useEffect(() => {
        if (indicator) {
        setColor(colors[indicator.severity]);
        }
    }, [indicator]);

    const show = (visible || !!indicator?.alwaysVisible) && indicator?.area === area;

    function boxShadow() {
        return `0 0 2px ${color}, 0 0 4px ${color},0 0 6px ${color}, 0 0 8px ${color}, 0 0 10px ${color}, 0 0 12px ${color}, 0 0 14px ${color}, 0 0 16px ${color}`;
    }

    return <div className={styles.Container}>
        <div className={classNameIf(show, styles.Indicator)}
             style={{backgroundColor: color, boxShadow: boxShadow()}}/>
        {children}
    </div>;
}

export default Indicator;
