import React, {useEffect, useRef, useState} from 'react';
import {useStoreActions, useStoreState} from "../store";
import {clock} from "../domain/Clock";
import {useHotkeysContext} from "../shared/hotkeys/useHotkeysContext";
import styles from "./NewTask.module.css"
import Icon from "../shared/Icon";
import {useHotkeys} from "../shared/hotkeys/useHotkeys";
import Tooltip from "../shared/components/Tooltip";
import {shortcutFrom} from "../shared/hotkeys/shortcutFrom";
import useSelection from "../shared/useSelection";
import {identity} from "lodash";
import {classNames} from "../shared/components/className";
import EstimationView from "../projects/manage/EstimationView";

function NewTask() {
    const [inputValue, setInputValue] = useState('');
    const [show, setShow] = useState(false);

    const inputRef = useRef<HTMLInputElement>(null);

    const labels = useStoreState(state => state.timeLabels);
    const values = [undefined, ...labels.map(label => label.name).reverse()]
    const {selectedId, next, prev} = useSelection(values, identity);

    const createNewTask = useStoreActions(actions => actions.createNewTask);

    const {enableDefaultScopes, enableOnlyScope} = useHotkeysContext();

    useEffect(() => {
        setTimeout(() => {
            if (show && inputRef.current) {
                inputRef.current.focus();
            }
        }, 0)
    }, [show]);

    function performOpenTaskCreation() {
        enableOnlyScope("task-creation")
        setShow(true)
        setInputValue("");
    }

    const CREATE_NEW_TASK_SHORTCUT = "n";
    useHotkeys(CREATE_NEW_TASK_SHORTCUT, () => {
        performOpenTaskCreation();
    }, {
        scope: "default"
    })
    useHotkeys("esc", () => {
        closeTaskCreation();
    }, {
        scope: "task-creation"
    })

    function closeTaskCreation() {
        enableDefaultScopes();
        setShow(false)
        return false;
    }


    const createTask = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            await createNewTask({
                content: inputValue,
                dueDate: clock.today(),
                estimation: selectedId
            })
        } finally {
            setInputValue("");
            enableDefaultScopes();
            setShow(false)
        }
        return false
    };

    return <>
        <Tooltip text={shortcutFrom(CREATE_NEW_TASK_SHORTCUT)}>
            <Icon className={styles.AddAction} name="plus.svg" size={32} onClick={performOpenTaskCreation}/>
        </Tooltip>
        {show && <div className={styles.Overlay}>
            <div className={styles.Modal}>
                <div className={styles.Close} onClick={closeTaskCreation}>
                    <Icon name="x.svg"/>
                </div>
                <form className={styles.Form} onSubmit={createTask}>
                    <label>
                        <input
                            ref={inputRef}
                            className={styles.Input}
                            placeholder="Task Name"
                            type="text"
                            value={inputValue}
                            onChange={(e) => setInputValue(e.target.value)}
                            onKeyDown={(e) => {
                                if (e.key === 'Escape') {
                                    e.preventDefault();
                                    closeTaskCreation();
                                }
                                if (e.key === 'ArrowUp') {
                                    e.preventDefault();
                                    prev();
                                }

                                if (e.key === 'ArrowDown') {
                                    e.preventDefault();
                                    next();
                                }
                            }}
                        />
                    </label>
                    <div className={styles.Estimate}>
                        <div style={{flex: 1}}>
                            <EstimationView label={labels.find(label => label.name === selectedId)}/>
                        </div>
                    </div>
                    <div className={styles.Actions}>
                        <div className={styles.Left}>
                            <button className={styles.Button} type="button"
                                    onClick={prev}>{shortcutFrom("up")}</button>
                            <button className={styles.Button} type="button"
                                    onClick={next}>{shortcutFrom("down")}</button>
                        </div>
                        <button className={classNames(styles.Button, styles.Create)} type="submit">Create</button>
                    </div>
                </form>
            </div>
        </div>}
    </>;
}

export default NewTask;
