import {useEffect, useState} from "react";
import {Minutes} from "../shared/Minutes";
import dayjs from "dayjs";

export function workCapacityLeft(targetTime: string) {
    const now = dayjs().startOf("minutes");
    const [hour, minutes] = targetTime.split(':');
    const target = dayjs().set('hour', parseInt(hour, 10))
        .set('minute', parseInt(minutes, 10))
        .startOf("minutes");

    return target.diff(now, 'minute');
}

function useMinutesUntil(targetTime: string): number {
    const [minutesLeft, setMinutesLeft] = useState<Minutes>(0);

    useEffect(() => {
        const updateMinutesLeft = () => {
            const diffInMinutes = workCapacityLeft(targetTime);
            setMinutesLeft(diffInMinutes);
        };

        updateMinutesLeft();

        const intervalId = setInterval(updateMinutesLeft, 60000);

        return () => clearInterval(intervalId);
    }, [targetTime]);

    return minutesLeft;
}

export default useMinutesUntil;
