import {useCallback, useState} from 'react';
import {useHotkeys} from "./hotkeys/useHotkeys";
import {HotKeyScope} from "./hotkeys/HotKeyScope";

export type ExtractIdFunction<T, U> = (item: T) => U;

export enum Orientation {
    NONE,
    HORIZONTAL,
    VERTICAL
}

export interface SelectionOptions {
    orientation: Orientation;
    scope: HotKeyScope;
}

export function idExtractor<T>(item: { id: T }) {
    return (item.id)
}

export function useSelection<T, U>(items: T[], extractId: ExtractIdFunction<T, U>, options: Partial<SelectionOptions> = {}) {
    const {orientation, scope} = defaults(options)
    const [selectedIndex, setSelectedIndex] = useState(0);

    const selectedId = items.length > 0 ? extractId(items[Math.min(selectedIndex, items.length - 1)]) : undefined;

    const next = useCallback(() => {
        setSelectedIndex((prevIndex) => (prevIndex + 1) % items.length);
    }, [items.length]);

    const prev = useCallback(() => {
        setSelectedIndex((prevIndex) =>
            (prevIndex - 1 + items.length) % items.length
        );
    }, [items.length]);

    const setSelectedId = (id: U) => {
        setSelectedIndex(items.map(extractId).findIndex(it => it === id));
    };

    useHotkeys("left", prev, {enabled: orientation === Orientation.HORIZONTAL, scope})
    useHotkeys("right", next, {enabled: orientation === Orientation.HORIZONTAL, scope})
    useHotkeys("up", prev, {enabled: orientation === Orientation.VERTICAL, scope})
    useHotkeys("down", next, {enabled: orientation === Orientation.VERTICAL, scope})

    return {
        selectedId,
        setSelectedId,
        next,
        prev,
    };
}

function defaults(options: Partial<SelectionOptions>): SelectionOptions {
    const fulfilled: SelectionOptions = {
        orientation: Orientation.NONE,
        scope: "default"
    };
    return {...fulfilled, ...options}
}

export default useSelection;
