import React from 'react';
import ProjectView from "./ProjectView";
import {TaskId} from "../shared/TaskId";
import Selectable from "../shared/components/Selectable";
import {ProjectItem} from "./ProjectsPage";
import Footer from "../layout/Footer";

import {TaskActions} from "./TaskActions";
import ChangePriority from "./ChangePriority";
import SidebarRight from "../layout/SidebarRight";

interface ProjectsProps {
    tasks: ProjectItem[];
    selectedId?: TaskId;
}

function ProjectList({tasks, selectedId}: ProjectsProps) {

    const task = tasks.find(({id})=> id === selectedId)
    const assignedPriority = task?.priority

    return <>
        <h1>Projects</h1>
        {tasks.map((task) => (
            <Selectable key={task.id} selected={task.id === selectedId}>
                <ProjectView
                    key={task.id}
                    task={task}
                    selected={task.id === selectedId}
                />
            </Selectable>
        ))}
        <SidebarRight>
            {selectedId &&
                <ChangePriority currentPriority={assignedPriority!} taskId={selectedId}/>}
        </SidebarRight>
        <Footer>
            <TaskActions task={task} selectedId={selectedId}/>
        </Footer>
    </>;
}

export default ProjectList;
