import React from 'react';
import {Label, LabelCategory} from "../domain/Label";
import styles from "./LabelsComp.module.css"

interface LabelsCompProps {
    category: LabelCategory,
    labels: Label[]
}

function LabelsComp({category, labels}: LabelsCompProps) {
    return <>
        <h3 className={styles.Category}>{category}</h3>
        <div className={styles.Labels}>
            {labels.map(label => (
                <div key={label.name} className={styles.Label}
                     style={{borderColor: label.color.value}}>{label.name}</div>
            ))}
        </div>
    </>;
}

export default LabelsComp;
