import React from 'react';
import {useStoreState} from "../store";
import {useNavigate, useParams} from "react-router-dom";
import {TaskId} from "../shared/TaskId";
import Icon from '../shared/Icon';
import styles from "./ProjectPage.module.css"
import ChangePriority from "./ChangePriority";
import Footer from "../layout/Footer";
import useSelection, {Orientation} from "../shared/useSelection";
import SidebarRight from "../layout/SidebarRight";
import {TaskActions} from "./TaskActions";
import {Project, projectFrom} from "../plan/projectFrom";
import ProjectOverview from "./ProjectOverview";
import AddTaskInProject from "./manage/AddTaskInProject";
import Header from "../layout/Header";
import {useHotkeys} from "../shared/hotkeys/useHotkeys";

function flattenProjects(items: Project[]): Project[] {
    return items.reduce((acc: Project[], item: Project) => {
        acc.push(item);
        acc.push(...flattenProjects(item.subtasks));
        return acc;
    }, []);
}

function ProjectPage() {
    const {id} = useParams<{ id: TaskId }>();
    const tasks = useStoreState(state => state.tasks);
    const {rootId, content} = tasks[id!];
    const navigate = useNavigate();
    const navigateToProjects = () => navigate("/projects")
    const project = projectFrom(Object.values(tasks), rootId);
    const flatTasks = flattenProjects([project])
    const {selectedId, setSelectedId} = useSelection(flatTasks, ({id}) => id, {orientation: Orientation.VERTICAL});

    useHotkeys("esc", navigateToProjects);

    const selectProject = (id: TaskId)=> setSelectedId(id);

    const task = flatTasks.find(task => task.id === selectedId);

    const assignedPriority = flatTasks.find(task => task.id === selectedId)?.priority!;
    return <>

        <Header>
            <div className={styles.Header}>
                <AddTaskInProject parentId={selectedId}/>
            </div>
        </Header>
        <div className={styles.MainHeader}>
            <Icon size={32} name="circle-left.svg" onClick={navigateToProjects} className={styles.BackAction}/>
            <h1>{content}</h1>
        </div>
        <ProjectOverview project={project} selectedId={selectedId} onSelect={selectProject}/>
        <SidebarRight>
            {selectedId &&
                <ChangePriority currentPriority={assignedPriority} taskId={selectedId}/>}
        </SidebarRight>
        <Footer>
            <TaskActions task={task} selectedId={selectedId}/>
        </Footer>
    </>;
}

export default ProjectPage;


