import {extractMinutesFrom, timeLabelRegex} from "./asLabels";
import {Estimation, RawTask} from "../domain/RawTask";
import {LabelName} from "../shared/LabelName";
import {TaskApiResponse} from "./TaskApiResponse";

export function findEstimationLabel(labels: LabelName[]): LabelName | undefined {
    return labels.find(labelName => timeLabelRegex.test(labelName));
}

export function tryExtractEstimationFrom(labels: LabelName[]): Estimation | undefined {
    const found = findEstimationLabel(labels)
    return found ? {code: found, duration: extractMinutesFrom(found)} : undefined;
}


export function asTaskFromApi(item: TaskApiResponse): RawTask {
    return {
        id: item.id,
        content: item.content,
        labels: item.labels,
        estimation: tryExtractEstimationFrom(item.labels),
        dueDate: item.due?.date,
        priority: item.priority,
        parentId: item.parent_id,
        completed: item.is_completed,
        order: item.order
    };
}

export function asTasksFromApi(input: TaskApiResponse[]): RawTask[] {
    return input.map(item => asTaskFromApi(item));
}

