import {TaskId} from "../shared/TaskId";
import {RootId} from "../shared/RootId";
import {Priority} from "../shared/Priority";
import {DueDate} from "../todoist/DueDate";
import {LabelName} from "../shared/LabelName";
import {Estimation} from "./RawTask";
import {dueDate} from "../shared/dueDate";

export enum TaskCategory {
    NODE = "NODE",
    LEAF = "LEAF",
}

export interface Task {
    id: TaskId;
    content: string;
    rootId: RootId;
    path: TaskId[];
    parentId?: TaskId;
    category: TaskCategory;
    dueDate?: DueDate;
    priority: Priority;
    labels: LabelName[];
    estimation?: Estimation;
    completed: boolean,
    order: number;
}

export function isProject({category, parentId}: Task): boolean {
    return category === TaskCategory.NODE && !parentId
}

export function scheduledTodayOrEarlier(task: Task): boolean {
    return !!task.dueDate && task.dueDate <= dueDate.today()
}


export function scheduledAtLestFor(limit: DueDate): (task: Task) => boolean {
    return function ({dueDate}: Task) :boolean{
        return !!dueDate && dueDate <= limit;
    }
}

export function scheduled({dueDate}: Task): boolean {
    return !!dueDate;
}

export function estimated({estimation}: Task): boolean {
    return !!estimation;
}


export function leaf({category}: Task): boolean {
    return category === TaskCategory.LEAF;
}

export function notEstimated({estimation}: Task): boolean {
    return !estimation;
}

export function isTaskToDo({category}: Task): boolean {
    return category === TaskCategory.LEAF
}

export function belongsTo(id: RootId): (task: Task) => boolean {
    return ({rootId}: Task) => (rootId === id)
}

export function byDueDate(a: Task, b: Task) {
    if (a.dueDate === b.dueDate) {
        return 0;
    }
    if (a.dueDate === undefined) {
        return 1;
    }
    if (b.dueDate === undefined) {
        return -1;
    }
    return a.dueDate.localeCompare(b.dueDate)
}

export function byPriority(a: Task, b: Task) {
    return b.priority - a.priority
}

export function byOrder(a: Task, b: Task) {
    return a.order - b.order
}
