import React from 'react';
import {Profile} from "../todoist/auth/Profile";
import styles from "./ProfileComp.module.css";
import Reveal from "./Reveal";
import ColorPicker from "../shared/components/color-picker/ColorPicker";
import {useStoreActions} from "../store";
import {useNavigate} from "react-router-dom";
import Enabled from "./Enabled";

interface AuthKeyCompProps {
    value: Profile
}

function ProfileComp({value: {id, name, value, active, color}}: AuthKeyCompProps) {
    const {changeProfile, enableProfile} = useStoreActions(actions => actions);
    const navigate = useNavigate();

    return <div className={styles.AuthKeyComp}>
        <ColorPicker color={color} onColorChange={(color) => {
            changeProfile({id, change: {color}})
        }}/>
        <div>{name}</div>
        <div  onClick={() => {
            enableProfile({id})
            navigate("/")
        }}>
            <Enabled value={active}/>
        </div>
        <Reveal value={value}/>

    </div>;
}

export default ProfileComp;
