import React from 'react';
import {Label} from "../../domain/Label";
import Icon from "../../shared/Icon";
import {ColorKey, getColorByKey} from "../../shared/Color";
import styles from "./EstimationView.module.css"
import DurationComp from "../../duration/components/DurationComp";
import {Estimation} from "../../domain/RawTask";
import {extractMinutesFrom} from "../../todoist/asLabels";

interface EstimationSelectorProps {
    label?: Label
}

function EstimationView({label}: EstimationSelectorProps) {

    const estimatoin: Estimation | undefined = label? {code: label.name, duration: extractMinutesFrom(label.name)}: undefined

    return <>
        {estimatoin ? <DurationComp estimation={estimatoin}/>:
            <div className={styles.EstimationView}><Icon name="ban.svg" color={getColorByKey(ColorKey.GREY)}/> <div className={styles.NoEstimation}>No estimate</div></div>}
    </>;
}

export default EstimationView;
