import React, {PropsWithChildren} from 'react';
import Toasts from "./support/Toasts";
import {useStoreRehydrated} from "easy-peasy";
import HotkeysProviderWithDefaultScopes from './shared/hotkeys/HotkeysProviderWithDefaultScopes';

function WaitForStateRehydration({children}: PropsWithChildren) {
    const isRehydrated = useStoreRehydrated();
    return <>
        {isRehydrated ? children : null}
    </>
}

function AppLibs({children}: PropsWithChildren) {
    return <>
        <Toasts/>
        <WaitForStateRehydration>
            <HotkeysProviderWithDefaultScopes>
                {children}
            </HotkeysProviderWithDefaultScopes>
        </WaitForStateRehydration>
    </>;
}

export default AppLibs;
