import React from 'react';
import {useStoreState} from "../store";
import ProfileComp from "./ProfileComp";
import Icon from "../shared/Icon";
import styles from "./ProfilePage.module.css";
import {useNavigate} from "react-router-dom";

function ProfilePage() {
    const profiles = useStoreState(state => state.profiles);
    const navigate = useNavigate();
    if (profiles.length === 0) {
        return <div>No Profiles</div>
    }

    return <>
        <div className={styles.Back}>
            <Icon size={32} name="circle-left.svg" onClick={() => navigate("/settings")} className={styles.BackAction}/>
        </div>

        <Icon size={32} name="plus.svg" onClick={() => navigate("/settings/profiles/add")}/>

        <div className={styles.Profiles}>
            {profiles.map(profile => <ProfileComp key={profile.id} value={profile}/>)}
        </div>
    </>;
}

export default ProfilePage;
