import dayjs from "../shared/dayjs";
import DayHeader from "./DayHeader";
import {DayOverview} from "./DayOverview";
import DayNavigation from "./DayNavigation";
import {Day, dayFrom} from "../domain/Day";
import {objectKeys} from "../shared/objectKeys";

interface OverviewProps {
    today: Day,
    overview: Record<Day, DayOverview>
}

const range = (n: number) => Array.from({length: n}, (value, key) => key)

function dayRange(start: Day, days: number): Day[] {
    let begin = dayjs(start);
    return range(days).map(ordinal => begin.add(ordinal, "days")).map(format)
}

function format(dayjs: dayjs.Dayjs): Day {
    return dayFrom(dayjs)
}

function Overview({today, overview}: OverviewProps) {
    let future = dayRange(today, 14);
    let days = Array.from(new Set<Day>([...future, ...objectKeys(overview)])).sort();
    return <div>
        {days.map(it => (<DayHeader key={it} today={today} value={it} dayOverview={overview[it]}/>))}
        <DayNavigation days={days}/>
    </div>
}

export default Overview
