import {Task, TaskCategory} from "../domain/Task";
import {TaskId} from "../shared/TaskId";
import {useStoreActions} from "../store";
import styles from "./TaskActions.module.css";
import {ModifierKey} from "../shared/components/OnKeyPressed";
import AssignEstimation from "./AssignEstimation";
import {findEstimationLabel} from "../todoist/asTasks";
import HotkeyConfirmation from "../shared/hotkeys/HotkeyConfirmation";
import React from "react";
import Icon from "../shared/Icon";
import DeleteTask from "../actions/components/DeleteTask";
import ScheduleTask from "./ScheduleTask";

export interface TaskActionsProps {
    task?: Task,
    selectedId?: TaskId
}

export function TaskActions({task, selectedId}: TaskActionsProps) {

    const {completeTask} = useStoreActions(actions => actions);


    const performCompleteTask = () => {
        if (selectedId) {
            completeTask(selectedId)
        }
    }

    const isLeaf = task?.category === TaskCategory.LEAF
    return <div className={styles.TaskActions}>
        <AssignEstimation taskId={selectedId!}
                          enabled={isLeaf && !!selectedId}
                          currentEstimation={findEstimationLabel(task?.labels || [])}/>
        <ScheduleTask taskId={selectedId!}
                      enabled={isLeaf && !!selectedId}/>
        <div className={styles.FinishActions}>
            <DeleteTask taskId={selectedId} disabled={!isLeaf}/>
            <HotkeyConfirmation disabled={!selectedId || !isLeaf} action={performCompleteTask}
                                binding={"enter"} label={<Icon name="check.svg"/>}/>
        </div>
    </div>
}
