import {useStoreState} from "../store";
import Working from "./Working";
import {scheduledTodayOrEarlier} from "../domain/Task";


function WorkPage() {
    const tasks = useStoreState(state => state.readyToDos)
        .filter(scheduledTodayOrEarlier)

    return <>
        {tasks.length > 0 ? <Working tasks={tasks}/> : <div>No tasks</div>}
    </>
}

export default WorkPage
