import React from 'react';
import {Hex} from "../shared/Color";
import styles from "./Dot.module.css"
import range from "../shared/range";
import {classNames} from "../shared/components/className";

interface DotProps {
    size?: number;
    color?: Hex;
    visible: boolean;
    opacity: number;
}

function Dot({size = 8, visible, opacity = 1, color}: DotProps) {
    return <div style={{width: size, height: size, visibility: visible ? "visible" : "hidden", opacity: opacity, backgroundColor: color}}
                className={styles.Dot}/>;
}

type DotsDirection = "left" | "right"

interface DotsProps {
    value: number;
    color?: Hex;
    limit?: number;
    direction: DotsDirection;
}

const dotsDirectionClass: Record<DotsDirection, string> = {
    "left": styles.Left,
    "right": styles.Right
}

export function Dots({value, color, direction, limit = 8}: DotsProps) {
    const amount = Math.abs(value);
    const calculateOpacity = (index: number) => {
        if (index < limit - 3) {
            return 1
        }
        if (index < limit - 2) {
            return .6
        }
        if (index < limit - 1) {
            return .3
        }
        return .1
    }
    return <div className={classNames(styles.Dots, dotsDirectionClass[direction])}>
        {range(Math.min(limit, amount)).map((n) => <Dot key={n} visible={true} color={color} size={8} opacity={calculateOpacity(n)}/>)}
    </div>
}

export default Dot;
