import React from 'react';
import {Shortcut} from "./SubActions";
import {useContextScope} from "./ScopeContext";
import {HotKey} from "../../hotkeys/HotKey";
import {Hex} from "../../Color";

interface SubActionProps {
    shortcut: Shortcut;
    label: string | React.ReactNode;
    color?: Hex;
    disabled?: boolean;
    onClick: () => void;
}

function SubAction({shortcut, onClick, label, color, disabled}: SubActionProps) {
    const {scope, onActionCompleted} = useContextScope();

    const performOnClick = () => {
        onClick();
        if (onActionCompleted) {
            onActionCompleted();
        }
    }
    return <HotKey
        binding={shortcut}
        label={label}
        onClick={performOnClick}
        scope={scope}
        color={color}
        disabled={disabled}
    />
}

export default SubAction;
