import React, {useState} from 'react';
import {TaskId} from "../../shared/TaskId";
import NewTaskInProject from "./NewTaskInProject";
import Icon from "../../shared/Icon";
import styles from "./AddTaskInProject.module.css"
import {classNameIf, classNames} from "../../shared/components/className";
import enabled from "../../settings/Enabled";
import {useHotkeysContext} from "../../shared/hotkeys/useHotkeysContext";
import {useHotkeys} from "../../shared/hotkeys/useHotkeys";
import Tooltip from "../../shared/components/Tooltip";
import {shortcutFrom} from "../../shared/hotkeys/shortcutFrom";

interface AddTaskInProjectProps {
    parentId?: TaskId;
}

function AddTaskInProject({parentId}: AddTaskInProjectProps) {
    const [show, setShow] = useState(false);

    const {enableDefaultScopes, enableOnlyScope} = useHotkeysContext();

    const ADD_TASK_SHORTCUT = "shift + n"

    useHotkeys(ADD_TASK_SHORTCUT, () => {
        performOpenTaskCreation();
    }, {
        scope: "default"
    })
    useHotkeys("esc", () => {
        closeTaskCreation();
    }, {
        scope: "task-creation"
    })

    function closeTaskCreation() {
        enableDefaultScopes();
        setShow(false)
        return false;
    }

    function performOpenTaskCreation() {
        if (parentId) {
            enableOnlyScope("task-creation")
            setShow(true)
        }
    }

    return <>
        <Tooltip text={shortcutFrom(ADD_TASK_SHORTCUT)}>
            <div className={classNames(styles.AddAction, classNameIf(!enabled, styles.Disabled))}
                 onClick={performOpenTaskCreation}>
                <Icon size={32} name="code-merge.svg"/>
                <Icon className={styles.SubIcon} name="plus.svg"/>
            </div>
        </Tooltip>

        {show && <NewTaskInProject parentId={parentId} show={show} onClose={closeTaskCreation}/>}
    </>;
}

export default AddTaskInProject;
