import {HotkeysProvider as ReactHotkeysProvider} from "react-hotkeys-hook";
import {HotKeyScope} from "./HotKeyScope";
import {PropsWithChildren} from "react";

export const DEFAULT_ACTIVE_SCOPES: HotKeyScope[] = ["default"]

function HotkeysProviderWithDefaultScopes({children}: PropsWithChildren) {
    return <ReactHotkeysProvider initiallyActiveScopes={DEFAULT_ACTIVE_SCOPES}>
        {children}
    </ReactHotkeysProvider>
}

export default HotkeysProviderWithDefaultScopes
