import {HotKeyScope} from "./HotKeyScope";
import {useHotkeys as reactUseHotkeys} from "react-hotkeys-hook"
import {HotkeyCallback, Keys} from "react-hotkeys-hook/src/types";

export type HotkeysContextType = {
    enableOnlyScope: (scope: HotKeyScope) => void
    enableDefaultScopes: () => void
}

export interface Options {
    scope: HotKeyScope;
    enabled: boolean;
}

export function useHotkeys(keys: Keys,
                           callback: HotkeyCallback,
                           option: Partial<Options> = {}) {
    const {enabled, scope} = options(option)
    reactUseHotkeys(keys, callback, {
        scopes: scope,
        enabled,
    });
}

function options(patch: Partial<Options>): Options {
    const fulfilled: Options = {
        scope: "default",
        enabled: true
    }
    return {...fulfilled, ...patch}
}
