import React from 'react';
import Navigate from "../shared/Navigate";
import {classNames} from "../shared/components/className";
import styles from "./Project.module.css"
import ProjectSummary from "./ProjectSummary";
import {ProjectItem} from "./ProjectsPage";
import DueDateItem from "./DueDateItem";
import {clock} from "../domain/Clock";
import ProjectIcon from "./ProjectIcon";

interface ProjectViewProps {
    task: ProjectItem;
    selected: boolean;
}

function ProjectView({task, selected}: ProjectViewProps) {
    return <Navigate to={`/projects/${task.id}`}>
        <div className={styles.Project}>
            <div className={styles.Header}>
                <DueDateItem today={clock.today()} date={task.projectDueDate}/>
            </div>
            <div className={classNames("text-large", styles.Main)}>
                <ProjectIcon category={task.category} priority={task.priority} selected={selected}/>
                <div className={styles.Content}>{task.content}</div>
            </div>
            <div className={classNames("text-small", styles.Footer)}>
                <ProjectSummary value={task.workableTasksAmount}/>
            </div>
        </div>
    </Navigate>;
}

export default ProjectView;
