import dayjs from "../shared/dayjs";
import {Period} from "../shared/Period";

export type Day = `${number}${number}${number}${number}-${number}${number}-${number}${number}`;
const DAY_REGEX = /^\d{4}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01])$/

export function dayFrom(dayjs: dayjs.Dayjs): Day {
    const output = dayjs.format("YYYY-MM-DD");
    if (!DAY_REGEX.test(output)) {
        throw new Error(`Created output: ${output} isn't valid format for Day`)
    }
    return output as Day
}

export function onDayAdd({amount, unit}: Period): Day {
    return dayFrom(dayjs().add(amount, unit))
}


