import React, {useEffect, useRef, useState} from 'react';
import {useStoreActions, useStoreState} from "../../store";
import {clock} from "../../domain/Clock";
import styles from "../../task/NewTask.module.css"
import Icon from "../../shared/Icon";
import {TaskCategory} from "../../domain/Task";
import TaskPath from "./TaskPath";
import {TaskId} from "../../shared/TaskId";
import EstimationView from "./EstimationView";
import useSelection from "../../shared/useSelection";
import {identity} from "lodash";
import {shortcutFrom} from "../../shared/hotkeys/shortcutFrom";
import {classNames} from "../../shared/components/className";


interface NewTaskInProjectProps {
    parentId?: TaskId;
    show: boolean;
    onClose: () => void;
}

function NewTaskInProject({parentId, show, onClose}: NewTaskInProjectProps) {
    const [inputValue, setInputValue] = useState('');

    const inputRef = useRef<HTMLInputElement>(null);

    const labels = useStoreState(state => state.timeLabels);
    const values = [undefined, ...labels.map(label => label.name)]
    const {selectedId, next, prev} = useSelection(values, identity);

    const createNewTask = useStoreActions(actions => actions.createNewTask);
    const {tasks} = useStoreState(state => state);

    const anySiblingScheduled = Object.values(tasks).filter(task => task.parentId === parentId).some(task => task.dueDate)

    const parent = parentId ? tasks[parentId] : undefined;

    useEffect(() => {
        setTimeout(() => {
            if (show && inputRef.current) {
                inputRef.current.focus();
            }
        }, 0)
    }, [show]);

    function closeTaskCreation() {
        onClose();
        return false;
    }

    const createTask = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            await createNewTask({
                content: inputValue,
                dueDate: anySiblingScheduled ? undefined : clock.today(),
                parentId: parentId,
                estimation: selectedId
            })
        } finally {
            setInputValue("");
            onClose();
        }
        return false
    };

    return <>
        {show && <div className={styles.Overlay}>
            <div className={styles.Modal}>
                <div className={styles.Close} onClick={closeTaskCreation}>
                    <Icon name="x.svg"/>
                </div>
                <form className={styles.Form} onSubmit={createTask}>
                    <label>
                        <input
                            ref={inputRef}
                            className={styles.Input}
                            placeholder="Task Name"
                            type="text"
                            value={inputValue}
                            onChange={(e) => setInputValue(e.target.value)}
                            onKeyDown={(e) => {
                                if (e.key === 'Escape') {
                                    e.preventDefault();
                                    closeTaskCreation();
                                }
                                if (e.key === 'ArrowUp') {
                                    e.preventDefault();
                                    prev();
                                }

                                if (e.key === 'ArrowDown') {
                                    e.preventDefault();
                                    next();
                                }
                            }}
                        />
                    </label>

                    {parent && <TaskPath path={parent.path} parentTaskContent={parent.content}
                                         changeType={parent.category === TaskCategory.LEAF}/>}

                    <div className={styles.Estimate}>
                        <div style={{flex: 1}}>
                            <EstimationView label={labels.find(label => label.name === selectedId)}/>
                        </div>
                    </div>
                    <div className={styles.Actions}>
                        <div className={styles.Left}>
                            <button className={styles.Button} type="button"
                                    onClick={prev}>{shortcutFrom("up")}</button>
                            <button className={styles.Button} type="button"
                                    onClick={next}>{shortcutFrom("down")}</button>
                        </div>
                        <button className={classNames(styles.Button, styles.Create)} type="submit">Create</button>
                    </div>
                </form>
            </div>
        </div>}
    </>
        ;
}

export default NewTaskInProject;
