import {LabelName} from "./shared/LabelName";
import {Label} from "./domain/Label";

export type Labels = Record<LabelName, Label>;

export function asRecordLabels(input: Label[]): Labels {
    const result: Labels = {};
    input.reduce((prev, curr) => {
        let name = curr.name;
        prev[name] = curr
        return prev;
    }, result);
    return result;
}
