import HotkeyConfirmation from "../shared/hotkeys/HotkeyConfirmation";
import {ColorKey, getColorByKey, Hex} from "../shared/Color";
import {period, Period} from "../shared/Period";
import {useHotkeys} from "../shared/hotkeys/useHotkeys";
import dayjs from "../shared/dayjs";

interface Action {
    label?: string,
    mod: "alt" | "shift",
    period: Period,
    color: Hex
}

interface Actions extends Omit<Action, "mod"> {
    binding: string
    label: string
}

function labelFrom({amount, unit}: Period): string {
    return dayjs().add(amount, unit).format("ddd")
}

const short: Action[] = [
    {
        label: "Today",
        mod: "alt",
        period: period(0, "days"),
        color: getColorByKey(ColorKey.BERRY_RED)
    },
    {
        label: "Tomorrow",
        mod: "alt",
        period: period(1, "day"),
        color: getColorByKey(ColorKey.RED)
    },
    {
        mod: "alt",
        period: period(2, "days"),
        color: getColorByKey(ColorKey.ORANGE)
    },
    {
        mod: "alt",
        period: period(3, "days"),
        color: getColorByKey(ColorKey.YELLOW)
    },
    {
        mod: "alt",
        period: period(4, "days"),
        color: getColorByKey(ColorKey.GREEN)
    },
    {
        mod: "alt",
        period: period(5, "days"),
        color: getColorByKey(ColorKey.LIME_GREEN)
    },
]


const long: Action[] = [
    {
        label: "1 Week",
        mod: "shift",
        period: period(1, "week"),
        color: getColorByKey(ColorKey.SKY_BLUE)
    },
    {
        label: "2 Weeks",
        mod: "shift",
        period: period(2, "weeks"),
        color: getColorByKey(ColorKey.LIGHT_BLUE)
    },
]

const actions: Actions [] = [
    ...short.map(({label, mod, period, ...rest}, index) => ({
        label: label ? label : labelFrom(period),
        period,
        binding: `${index + 1}`,
        ...rest,
    })),
    ...long.map(({label, mod, period, ...rest}, index) => ({
        label: label ? label : labelFrom(period),
        period,
        binding: `${mod} + ${index + 1}`,
        ...rest,
    })),
];

interface RescheduleDayProps {
    onReschedule: (period: Period) => void
}

export function RescheduleDay({onReschedule}: RescheduleDayProps) {

    return <>
        {actions.map(({label, period, color, binding}, index) => {
            return <HotkeyConfirmation
                key={index}
                binding={binding}
                action={() => {
                    onReschedule(period)
                }}
                label={label}
                color={color}
            />
        })}
    </>;
}
