import React from 'react';
import {TaskCategory} from "../domain/Task";
import Doable from "./Doable";
import Icon from "../shared/Icon";
import {priorityColor} from "../shared/priorityColor";
import {Priority} from "../shared/Priority";

interface ProjectIconProps {
    category: TaskCategory,
    priority: Priority,
    selected: boolean,

}

function ProjectIcon({category, priority, selected}: ProjectIconProps) {
    return <>
        {category === TaskCategory.LEAF ?
            <Doable selected={selected} priority={priority}/> :
            <Icon name={"folder.svg"} color={priorityColor(priority)}/>
        }
    </>;
}

export default ProjectIcon;
