import React from 'react';
import {TaskId} from "../shared/TaskId";
import {Project} from "../plan/projectFrom";
import styles from "./ProjectOverview.module.css"
import {classNames} from "../shared/components/className";
import DueDateItem from "./DueDateItem";
import {clock} from "../domain/Clock";
import Selectable from "../shared/components/Selectable";
import ProjectIcon from "./ProjectIcon";
import DurationComp from "../duration/components/DurationComp";

interface Props {
    project: Project;
    selectedId?: TaskId;
    onSelect: (id: TaskId) => void
}

function ProjectOverview({project: {subtasks, ...project}, selectedId, onSelect}: Props) {
    const selected = project.id === selectedId;

    const performSelection = (e: any) => {
        onSelect(project.id);
        e.preventDefault()
    }

    return <>
        <Selectable selected={selected}>
            <div className={styles.Project} onClick={performSelection}>
                <div className={classNames("text-large", styles.Main)}>
                    <ProjectIcon category={project.category} priority={project.priority} selected={selected}/>
                    <div className={styles.Content}>{project.content}</div>
                </div>
                <div className={styles.Header}>
                    <DueDateItem today={clock.today()} date={project.dueDate}/>
                </div>
                <div className={styles.Footer}>
                    {project.estimation && <DurationComp estimation={project.estimation} colored={selected}/>}
                </div>
            </div>
        </Selectable>
        <div className={styles.Subtasks}>
            {subtasks.map(subtask => <ProjectOverview key={subtask.id} project={subtask} selectedId={selectedId}
                                                      onSelect={onSelect}/>)}
        </div>
    </>;
}

export default ProjectOverview;
