import {HotKeyScope} from "./HotKeyScope";
import {useHotkeysContext as reactUseHotkeysContext} from "react-hotkeys-hook"
import {DEFAULT_ACTIVE_SCOPES} from "./HotkeysProviderWithDefaultScopes";
import difference from "lodash/difference";

export type HotkeysContextType = {
    enableOnlyScope: (scope: HotKeyScope) => void
    enableDefaultScopes: () => void
    enabledScopes: string []
}

export function useHotkeysContext(): HotkeysContextType {
    const {enableScope, disableScope, enabledScopes} = reactUseHotkeysContext();

    return {
        enableDefaultScopes: () => {
            DEFAULT_ACTIVE_SCOPES.forEach(enableScope);
            difference(enabledScopes, DEFAULT_ACTIVE_SCOPES).forEach(disableScope)
        },
        enableOnlyScope: (scope: HotKeyScope) => {
            enableScope(scope);
            DEFAULT_ACTIVE_SCOPES.forEach(disableScope);
        },
        enabledScopes: enabledScopes
    }
}
