import {WorkableTask} from "./Progress";
import {DoableTask} from "./DoableTask";

export function asWorkableTasks(items: DoableTask[]): WorkableTask[] {
    return items.reduce((acc: WorkableTask[], item: DoableTask) => {
        const previousDuration = acc.length > 0 ? acc[acc.length - 1].usedTime : 0;
        const newItem: WorkableTask = {
            usedTime: previousDuration + item.estimation.duration,
            ...item
        };
        acc.push(newItem);
        return acc;
    }, []);
}
