import styles from "./TaskItem.module.css"
import {DailyTask} from "./DailyTask";
import {classNameIf, classNames} from "../shared/components/className";
import {TaskId} from "../shared/TaskId";
import React from "react";
import Path from "../shared/components/Path";
import Doable from "../projects/Doable";
import DurationComp from "../duration/components/DurationComp";

interface TaskItemProps {
    task: DailyTask;
    onSelectTask: (taskId: TaskId) => void;
    reflectDuration: boolean
}

function TaskItem({task: {id, selected, content, estimation, path, priority}, onSelectTask, reflectDuration}: TaskItemProps) {
    return <div className={classNames(styles.TaskItem, classNameIf(selected, styles.Selected))}
                onClick={() => onSelectTask(id)}>
        <div className={styles.Header}>
            <Doable priority={priority} selected={selected}/>
            <div className={styles.Content}>
                <div className="text-large">{content}</div>
                <Path path={path}/>
            </div>
        </div>
        <DurationComp colored={selected} estimation={estimation}/>
    </div>
}

export default TaskItem
