import {Priority} from "../Priority";
import {priorityColor} from "../priorityColor";
import styles from "./PriorityIcon.module.css"


interface PriorityIconProps{
    value: Priority;
}

function PriorityIcon({value}: PriorityIconProps) {
    const color = priorityColor(value);
    return <div className={styles.Priority} style={{backgroundColor: color, borderColor: color}}/>
}
export default PriorityIcon
