import React from 'react';
import {Priority} from "../shared/Priority";
import styles from "./PriorityView.module.css"
import {priorityColor} from "../shared/priorityColor";
interface PriorityViewProps {
    value: Priority
}

function PriorityView({value}: PriorityViewProps) {
    const color = priorityColor(value);
    return <div className={styles.Priority} style={{backgroundColor: color}}/>;
}

export default PriorityView;
