import React from 'react';
import Icon from '../../shared/Icon';
import {TaskId} from "../../shared/TaskId";
import {useStoreState} from "../../store";
import styles from "./TaskPath.module.css"
import {classNameIf, classNames} from "../../shared/components/className";

interface TaskPathProps {
    path?: TaskId []
    parentTaskContent?: string;
    changeType: boolean;
}

function TaskPath({parentTaskContent, path, changeType}: TaskPathProps) {
    const tasks = useStoreState(state => state.tasks);
    const pathNames = [path?.map(item => tasks[item].content || item), parentTaskContent].join(" / ")
    return <div className={styles.PathNames}><Icon className={classNames(styles.PathIcon, classNameIf(changeType, styles.ChangeType))} name="code-merge.svg"/>{pathNames}</div>;
}

export default TaskPath;
