import React from 'react';
import {HotKey} from "../shared/hotkeys/HotKey";
import usePlanNavigation from "./planNavigate";
import {Day} from "../domain/Day";
import {usePlanParam} from "./usePlanParam";

interface DayNavigationProps {
    days: Day[];
}

function DayNavigation({days}: DayNavigationProps) {
    const {day} = usePlanParam();
    const {planFor} = usePlanNavigation();


    function prevDay() {
        const index = days.indexOf(day);
        const prevIndex = Math.max(0, index - 1);
        planFor(days[prevIndex])
    }

    function nextDay() {
        const index = days.indexOf(day);
        const nextIndex = Math.min(days.length - 1, index + 1);
        planFor(days[nextIndex])
    }

    return <div>
        <HotKey binding="meta + up" label="up" onClick={prevDay}/>
        <HotKey binding="meta + down" label="down" onClick={nextDay}/>
    </div>;
}

export default DayNavigation;
