import {DueDate, dueDateFrom} from "../todoist/DueDate";
import dayjs from "./dayjs";
import {PeriodUnit} from "./Period";

export namespace dueDate {
    export function today(): DueDate {
        return dueDateFrom(dayjs());
    }

    export function tomorrow(): DueDate {
        return dueDateFrom(dayjs().add(1, "day"));
    }

    export function future(value: number, unit: PeriodUnit): DueDate {
        return dueDateFrom(dayjs().add(value, unit));
    }
}
