import React from 'react';
import {TaskId} from "../shared/TaskId";
import {useStoreActions, useStoreState} from "../store";
import Icon from "../shared/Icon";
import {Hexes} from "../shared/hotkeys/hexes";
import styles from "./RescheduleTask.module.css"
import SubActions, {Shortcut} from "../shared/components/sub-actions/SubActions";
import SubAction from "../shared/components/sub-actions/SubAction";
import {parseDayJsFrom} from "../todoist/DueDate";
import dayjs from "../shared/dayjs";
import {ColorKey, getColorByKey, Hex} from "../shared/Color";

interface ScheduleTaskProps {
    taskId?: TaskId;
}


interface RescheduleOption {
    days: number;
    label: string;
    shortcut: Shortcut;
    color: Hex
}

const options: RescheduleOption[] = [
    {
        shortcut: "shift + 3",
        days: -7,
        label: "-1w",
        color: getColorByKey(ColorKey.RED)
    },
    {
        shortcut: "shift + 2",
        days: -2,
        label: "-2d",
        color: getColorByKey(ColorKey.ORANGE)
    },
    {
        shortcut: "shift + 1",
        days: -1,
        label: "-1d",
        color: getColorByKey(ColorKey.YELLOW)
    },
    {
        shortcut: "1",
        days: 1,
        label: "+1d",
        color: getColorByKey(ColorKey.LIME_GREEN)
    },
    {
        shortcut: "2",
        days: 2,
        label: "+2d",
        color: getColorByKey(ColorKey.GREEN)
    },
    {
        shortcut: "3",
        days: 7,
        label: "+1w",
        color: getColorByKey(ColorKey.BLUE)
    },
]

function RescheduleTask({taskId}: ScheduleTaskProps) {

    const tasks = useStoreState(state => state.tasks);

    const {rescheduleTask2, unscheduleTask} = useStoreActions(actions => actions)

    const performReschedule = (days: number) => {
        if (taskId && task.dueDate) {
            const dueDate = parseDayJsFrom(task.dueDate);
            const daysDiff = dueDate.diff(dayjs().startOf('day'), "days");
            rescheduleTask2({taskId, period: {unit: "days", amount: daysDiff + days}});
        }
    }
    const performUnschedule = () => {
        if (taskId) {
            unscheduleTask({taskId})
        }
    }

    const task = tasks[taskId || ""];

    function dayName(days: number): string {
        if (task && task.dueDate) {
            return parseDayJsFrom(task.dueDate).add(days, "days").format("ddd")
        }
        return ""
    }

    return <div className={styles.ScheduleTask}>
        <SubActions shortcut="alt + S" label={<Icon name="calendar-days.svg"/>} disabled={!taskId}>
            <SubAction
                key="unschedule"
                shortcut="0"
                color={Hexes.UNSHEDULE}
                label={<Icon name="ban.svg" size={12}/>}
                onClick={performUnschedule}
            />
            {options.map(({label, shortcut, days, color}, index) => {
                const actionLabel = `${label} (${dayName(days)})`;
                return <SubAction key={index}
                                  shortcut={shortcut ? shortcut : `${index + 1}` as Shortcut}
                                  label={actionLabel}
                                  color={color}
                                  onClick={() => {
                                      performReschedule(days);
                                  }}/>
            })
            }
        </SubActions>

    </div>;
}

export default RescheduleTask;
