import React from 'react';
import styles from "./ScheduleWeekView.module.css"
import {DueDate, dueDateFrom} from "../todoist/DueDate";
import dayjs from "../shared/dayjs";
import {DoableTask} from "../work/DoableTask";
import ScheduleDayView from "./ScheduleDayView";
import {Dayjs} from "dayjs";

interface ScheduleWeekViewProps {
    tasks: DoableTask[]
}

export enum DayOfWeek {
    Monday = "Monday",
    Tuesday = "Tuesday",
    Wednesday = "Wednesday",
    Thursday = "Thursday",
    Friday = "Friday",
    Saturday = "Saturday",
    Sunday = "Sunday"
}

export type WeekDayId = 1 | 2 | 3 | 4 | 5 | 6 | 7;


export function weekDayIdFrom(date: DueDate): WeekDayId {
    return dayjs(date).isoWeekday() as WeekDayId;
}

export interface DaySchedule {
    day: DueDate,
    tasks: DoableTask[]
}

type Week = {
    [day: number]: DaySchedule;
};

type GroupedByWeeks = Week[];

function groupTasksByWeek(tasks: DoableTask[]): GroupedByWeeks {
    const getMonday = (date: string): Dayjs => {
        return dayjs(date).startOf('isoWeek');
    };

    const getDayNumber = (date: string): number => {
        return dayjs(date).isoWeekday();
    };

    tasks.sort((a, b) => dayjs(a.dueDate).unix() - dayjs(b.dueDate).unix());

    const weeks: Record<string, Week> = {};

    for (const task of tasks) {
        const monday = getMonday(task.dueDate);

        const mondayKey = dueDateFrom(monday)

        if (!weeks[mondayKey]) {
            weeks[mondayKey] = {
                1: {day: dueDateFrom(monday.add(0, 'day')), tasks: []},
                2: {day: dueDateFrom(monday.add(1, 'day')), tasks: []},
                3: {day: dueDateFrom(monday.add(2, 'day')), tasks: []},
                4: {day: dueDateFrom(monday.add(3, 'day')), tasks: []},
                5: {day: dueDateFrom(monday.add(4, 'day')), tasks: []},
                6: {day: dueDateFrom(monday.add(5, 'day')), tasks: []},
                7: {day: dueDateFrom(monday.add(6, 'day')), tasks: []},
            };
        }

        const dayNumber = getDayNumber(task.dueDate);
        weeks[mondayKey][dayNumber].tasks.push(task);
    }

    const allWeeks: GroupedByWeeks = [];

    if (tasks.length > 0) {
        const startWeek = getMonday(tasks[0].dueDate);
        const endWeek = getMonday(tasks[tasks.length - 1].dueDate);

        let currentWeek = dayjs(startWeek);
        while (!currentWeek.isAfter(dayjs(endWeek), 'week')) {
            const currentWeekKey = currentWeek.format('YYYY-MM-DD');
            if (!weeks[currentWeekKey]) {
                weeks[currentWeekKey] = {
                    1: { day: dueDateFrom(currentWeek.add(0, 'day')), tasks: [] },
                    2: { day: dueDateFrom(currentWeek.add(1, 'day')), tasks: [] },
                    3: { day: dueDateFrom(currentWeek.add(2, 'day')), tasks: [] },
                    4: { day: dueDateFrom(currentWeek.add(3, 'day')), tasks: [] },
                    5: { day: dueDateFrom(currentWeek.add(4, 'day')), tasks: [] },
                    6: { day: dueDateFrom(currentWeek.add(5, 'day')), tasks: [] },
                    7: { day: dueDateFrom(currentWeek.add(6, 'day')), tasks: [] },
                };
            }
            allWeeks.push(weeks[currentWeekKey]);
            currentWeek = currentWeek.add(1, 'week');
        }
    }

    return allWeeks;

}

const daysOrder: DayOfWeek[] = [
    DayOfWeek.Monday,
    DayOfWeek.Tuesday,
    DayOfWeek.Wednesday,
    DayOfWeek.Thursday,
    DayOfWeek.Friday,
    DayOfWeek.Saturday,
    DayOfWeek.Sunday
];

function ScheduleWeekView({tasks}: ScheduleWeekViewProps) {

    const weeks = groupTasksByWeek(tasks);
    return <div key="ScheduleWeekView" className={styles.ScheduleWeekView}>
        {daysOrder.map(it => <div key={it}>{it}</div>)}

        {weeks.map((item, row) =>
            Object.entries(item).map(([key, value]) => (
                <div key={`day-${row}-${key}`}
                     style={{gridRow: row + 2, gridColumn: key}}>
                    <ScheduleDayView key={`${row}-${key}`} schedule={value}/>
                </div>
            ))
        )}

    </div>
    ;
}

export default ScheduleWeekView;
