import {useEffect} from "react";
import styles from "./Initialize.module.css"
import Svg from "../shared/Svg";
import {useStoreActions, useStoreState} from "../store";
import {useNavigate} from "react-router-dom";
import {getApiKey} from "../todoist/getApiKey";


function Initialize() {
    let navigate = useNavigate();

    const {fetchTasksAndLabels, addProfileWithKey} = useStoreActions((actions) => actions);

    const profiles = useStoreState(state => state.profiles);

    useEffect(() => {

        const apiKey = getApiKey();
        if (apiKey) {
            addProfileWithKey({value: apiKey})
        }

        if (profiles.length === 0) {
            navigate("/settings/profiles/add")
            return;
        }
        fetchTasksAndLabels();
        navigate("/estimate");
    }, []);

    return <div className={styles.Initialize}>
        <div className={styles.Loader}>
            <Svg size={128} src="/assets/icons/settings.svg"/>
        </div>
    </div>;
}

export default Initialize
