import styles from "./Timing.module.css"
import {Hex} from "../shared/Color";
import {LabelName} from "../shared/LabelName";
import Skeleton from "../shared/components/Skeleton";
import {useHotkeys} from "../shared/hotkeys/useHotkeys";

interface TimingProps {
    binding: string,
    label?: string,
    value: LabelName,
    color: Hex,
    disabled: boolean
    onClick: () => void
}

export function Timing({value, label, binding, color, disabled, onClick}: TimingProps) {
    async function performOnClick() {
        if (!disabled) {
            onClick()
        }
    }

    useHotkeys(binding, async () => {
        await performOnClick();
    }, {enabled: !disabled})

    return <button className={styles.Timing} onClick={() => performOnClick()}>
        <Skeleton enabled={disabled}>
            <div className={styles.Binding}
                 style={{backgroundColor: color, borderColor: color}}>
                {label ? label : binding}
            </div>
            <div className={styles.Value} style={{color: color}}>
                {value}
            </div>
        </Skeleton>
    </button>


}
