import React from 'react';
import {useStoreState} from "../store";
import ProjectList from "./ProjectList";
import {useHotkeys} from "../shared/hotkeys/useHotkeys";
import useSelection, {Orientation} from "../shared/useSelection";
import {and} from "../shared/and";
import {useNavigate} from "react-router-dom";
import {belongsTo, byDueDate, byPriority, isProject, isTaskToDo, leaf, Task, TaskCategory} from "../domain/Task";
import {or} from "../shared/or";
import {not} from "../shared/not";
import {isRoot} from "../domain/RawTask";
import {by} from "../shared/by";
import {RootId} from "../shared/RootId";
import {DueDate} from "../todoist/DueDate";
import Header from "../layout/Header";
import AddTaskInProject from "./manage/AddTaskInProject";
import styles from "./ProjectsPage.module.css"

export interface ProjectItem extends Task {
    workableTasksAmount: number;
    projectDueDate?: DueDate;
}

export function single({category, parentId}: Task): boolean {
    return category === TaskCategory.LEAF && !parentId
}

export function notEstimated({estimation}: Task): boolean {
    return !estimation
}

const byWorkableAmount = (a: ProjectItem, b: ProjectItem) => b.workableTasksAmount - a.workableTasksAmount;

function ProjectsPage() {
    const tasks = Object.values(useStoreState(state => state.tasks));
    const navigate = useNavigate();

    const minDueDate: Record<RootId, DueDate> = {};
    tasks.filter(leaf).reduce((acc, curr) => {
        const currDueDate = curr.dueDate;
        if (currDueDate) {
            const projectDueDate = acc[curr.rootId];
            if (projectDueDate && currDueDate < projectDueDate) {
                acc[curr.rootId] = currDueDate
            } else {
                acc[curr.rootId] = currDueDate
            }
        }
        return acc;
    }, minDueDate)

    const projects: ProjectItem[] = tasks.filter(or(isProject, and(single, notEstimated))).map(task => ({
        workableTasksAmount: tasks.filter(and(isTaskToDo, belongsTo(task.rootId), not(isRoot))).length,
        projectDueDate: minDueDate[task.rootId],
        ...task,
    })).sort(by(byDueDate, byPriority, byWorkableAmount));

    const {selectedId} = useSelection(projects, (task) => task.id, {orientation: Orientation.VERTICAL});

    useHotkeys("enter", () => {
        navigate(`/projects/${selectedId}`)
    })


    return <>
        <Header>
            <div className={styles.Header}>
                <AddTaskInProject parentId={selectedId}/>
            </div>
        </Header>
        <ProjectList tasks={projects} selectedId={selectedId}/>
    </>;
}

export default ProjectsPage;
