import React from 'react';
import {ColorKey, getColorByKey, Hex} from "../shared/Color";
import dayjs from "../shared/dayjs";
import styles from "./Stopwatch.module.css"
import Skeleton from "../shared/components/Skeleton";

interface StopwatchProps {
    started: boolean;
    loading: boolean;
    size?: number;
    strokeWidth?: number;
    duration: number;
    elapsedTime?: number;
    color?: Hex
}

interface CircleStyle {
    color: Hex,
    opacity: number,
}

interface StopwatchStyle {
    progress: CircleStyle,
    elapsed: CircleStyle,
    textColor?: Hex
}

function overTimeStyle(color: Hex): StopwatchStyle {
    return {
        elapsed: {
            opacity: 1,
            color: "#262626"
        },
        progress: {
            opacity: 1,
            color: color
        },
        textColor: color
    };
}

function onTimeStyle(color: Hex): StopwatchStyle {
    return {
        elapsed: {
            opacity: 1,
            color: color
        },
        progress: {
            opacity: .7,
            color: "#000000"
        },
        textColor: color
    };
}

function notStarted(color: Hex): StopwatchStyle {
    return {
        elapsed: {
            opacity: .2,
            color: color
        },
        progress: {
            opacity: .7,
            color: "#000000"
        },
        textColor: color
    };
}

function Stopwatch({
                       size = 256,
                       strokeWidth = 32,
                       duration,
                       elapsedTime = 0,
                       color = "#e6e6e6",
                       started,
                       loading
                   }: StopwatchProps) {
    const radius = (size - strokeWidth) / 2;
    const circumference = 2 * Math.PI * radius;

    const roundsCompleted = Math.floor((elapsedTime - 1) / duration);
    const effectiveElapsedTime = elapsedTime % duration;

    const progress = Math.min(effectiveElapsedTime / duration, 1);
    const dashOffset = circumference - progress * circumference;

    const formatTime = (timeInSeconds: number): string => {
        const duration = dayjs.duration(timeInSeconds, "seconds");
        if (duration.hours() > 0) {
            return duration.format("H:mm:ss")
        }
        return duration.format("m:ss")
    };

    let displayTime: string = "--:--";
    if (elapsedTime <= duration) {
        const timeLeft = duration - elapsedTime;
        displayTime = formatTime(timeLeft);
    } else {
        const extraTime = elapsedTime - duration;
        displayTime = `-${formatTime(extraTime)}`;
    }


    const styleProgress = (roundsCompleted: number): StopwatchStyle => {
        if (!started) {
            return notStarted(color)
        }
        if (roundsCompleted === 1) {
            return overTimeStyle(getColorByKey(ColorKey.YELLOW));
        }
        if (roundsCompleted === 2) {
            return overTimeStyle(getColorByKey(ColorKey.ORANGE));
        }
        if (roundsCompleted > 2) {
            return overTimeStyle(getColorByKey(ColorKey.RED));
        }
        return onTimeStyle(color)
    }

    let progressStyles = styleProgress(roundsCompleted);


    return (
        <Skeleton enabled={loading}>
            <div className={styles.WatchFace} style={{position: 'relative', width: size, height: size}}>
                <svg width={size} height={size}>
                    <circle
                        stroke={progressStyles.elapsed.color}
                        opacity={progressStyles.elapsed.opacity}
                        fill="transparent"
                        strokeWidth={strokeWidth}
                        r={radius}
                        cx={size / 2}
                        cy={size / 2}
                    />
                    <circle
                        stroke={progressStyles.progress.color}
                        opacity={progressStyles.progress.opacity}
                        fill="transparent"
                        strokeWidth={strokeWidth}
                        r={radius}
                        cx={size / 2}
                        cy={size / 2}
                        strokeDasharray={circumference}
                        strokeDashoffset={dashOffset}
                        style={{transition: 'stroke-dashoffset 0.5s ease, stroke 0.5s ease'}}
                        transform={`rotate(-90 ${size / 2} ${size / 2})`}
                    />
                </svg>
                <div
                    style={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        fontSize: '64px',
                        fontWeight: 'bold',
                        color: progressStyles.textColor,
                    }}
                >
                    {displayTime}
                </div>
            </div>
        </Skeleton>
    );
};

export default Stopwatch;
