
export enum ColorKey {
    BERRY_RED = 'berry_red',
    RED = 'red',
    ORANGE = 'orange',
    YELLOW = 'yellow',
    OLIVE_GREEN = 'olive_green',
    LIME_GREEN = 'lime_green',
    GREEN = 'green',
    MINT_GREEN = 'mint_green',
    TURQUOISE = 'turquoise',
    SKY_BLUE = 'sky_blue',
    LIGHT_BLUE = 'light_blue',
    BLUE = 'blue',
    GRAPE = 'grape',
    VIOLET = 'violet',
    LAVENDER = 'lavender',
    MAGENTA = 'magenta',
    SALMON = 'salmon',
    CHARCOAL = 'charcoal',
    GREY = 'grey',
    TAUPE = 'taupe',
    DEFAULT = 'default',
}

export type Hex = `#${string & ( `${string}${string}${string}` | `${string}${string}${string}${string}${string}${string}`)}`;

export interface Color {
    key: ColorKey,
    value: Hex
}

const defaultColor: Color = {
    key: ColorKey.DEFAULT,
    value: '#ffffff'
}

export function getColorByKey(key: ColorKey): Hex {
    return getColorObjByKey(key).value
}

export function getColorObjByKey(key: ColorKey): Color {
    return colors.find(color => color.key === key) || defaultColor;
}



