import React from 'react';
import {useStoreState} from "../store";
import {Label, LabelCategory} from "../domain/Label";
import LabelsComp from "./LabelsComp";

function LabelsPage() {
    const labels = useStoreState(state => state.labels);

    const groupeByCategory: Record<LabelCategory, Label[]> = {
        [LabelCategory.ESTIMATION]: [],
        [LabelCategory.PROJECT]: [],
        [LabelCategory.BASIC]: [],
        [LabelCategory.EXECUTION]: [],
        [LabelCategory.VALUE]: [],
        [LabelCategory.PRIORITIZATION]: []
    }


    return <>
        <h2>
            Labels
        </h2>
        {Object.entries(Object.values(labels)
            .reduce((acc, label) => {
                acc[label.category].push(label)
                return acc
            }, groupeByCategory))
            .map(([category, labels]) => (<>
                <LabelsComp key={category} category={category as LabelCategory} labels={labels}/>
            </>))}
    </>;
}

export default LabelsPage;
