import React from 'react';
import {TaskId} from "../shared/TaskId";
import {useStoreActions, useStoreState} from "../store";
import {LabelName} from "../shared/LabelName";
import SubActions, {Shortcut} from "../shared/components/sub-actions/SubActions";
import SubAction from "../shared/components/sub-actions/SubAction";
import Icon from "../shared/Icon";

interface AssignEstimationProps {
    taskId: TaskId;
    enabled: boolean;
    currentEstimation?: LabelName;
}

function AssignEstimation({taskId, enabled, currentEstimation}: AssignEstimationProps) {

    const assignEstimation = useStoreActions(actions => actions.assignEstimation)
    const timeLabels = useStoreState(state => state.timeLabels)

    const performAssignEstimation = (label: LabelName) => {
        assignEstimation({taskId, label})
    }
    return <>
        <SubActions shortcut="E" label={<Icon name="stopwatch.svg"/>} disabled={!enabled}>
            {timeLabels.map((label, index) =>
                <SubAction key={index}
                        shortcut={`${index + 1}` as Shortcut}
                        disabled={!enabled || label.name === currentEstimation}
                        color={label.color.value}
                        label={label.name}
                        onClick={() => {
                            performAssignEstimation(label.name)
                        }}/>)
            }
        </SubActions>

    </>;
}

export default AssignEstimation;
