import {LabelName} from "../shared/LabelName";
import {Minutes} from "../shared/Minutes";
import {TaskId} from "../shared/TaskId";
import {DueDate} from "../todoist/DueDate";
import {Priority} from "../shared/Priority";
import {Order} from "./Order";

export interface RawTask {
    id: TaskId;
    content: string;
    labels: LabelName[];
    dueDate?: DueDate;
    priority: Priority;
    estimation?: Estimation;
    order: Order;
    completed: boolean,
    parentId?: TaskId;
}

export function isRoot({parentId}: RawTask): boolean {
    return !parentId
}

export interface Estimation {
    code: LabelName;
    duration: Minutes;
}
