import {
    action,
    Action,
    Actions,
    computed,
    Computed,
    createStore,
    createTypedHooks,
    persist,
    thunk,
    Thunk,
    thunkOn,
    ThunkOn
} from 'easy-peasy';
import {asLabels, isTimeLabel} from "./todoist/asLabels";
import {LabelName} from "./shared/LabelName";
import {isRoot, RawTask} from "./domain/RawTask";
import {TaskId} from './shared/TaskId';
import {Priority} from "./shared/Priority";
import {asTaskFromApi, asTasksFromApi, tryExtractEstimationFrom} from "./todoist/asTasks";
import {sortTasks} from "./estimate/sortTasks";
import {DoableTask} from "./work/DoableTask";
import asDoableTask from "./work/asDoableTask";
import {clock} from "./domain/Clock";
import {DayOverview} from "./plan/DayOverview";
import {Label, LabelCategory} from "./domain/Label";
import {InProgressTask} from "./work/InProgressTask";
import dayjs from "dayjs";
import {DueDate, dueDateFrom} from "./todoist/DueDate";
import {Period} from "./shared/Period";
import {Day} from "./domain/Day";
import {Area, Severity} from "./shared/components/Indicator";
import {IndicatorStore} from "./IndicatorStore";
import {Minutes} from "./shared/Minutes";
import {workCapacityLeft} from "./work/useMinutesUntil";
import {LABELS_DEFINITION} from "./init/labelsConfiguration";
import {Tasks} from "./Tasks";
import {UUID} from "./shared/UUID";
import randomUUID from "./shared/randomUUID";
import {asRecord} from "./shared/asRecord";
import {dfs, dfsTree} from "./projects/dfs";
import {belongsTo, estimated, leaf, scheduled, Task, TaskCategory} from "./domain/Task";
import {nextProfileId, Profile, ProfileId} from "./todoist/auth/Profile";
import {Hex} from "./shared/Color";
import {and} from "./shared/and";
import {assignEstimation} from "./removeEstimations";
import {findEstimableFrom} from "./estimate/findEstimableFrom";
import {ApiKey} from "./todoist/auth/ApiKey";
import {asRecordLabels} from "./Labels";
import {offlineMiddleware} from "./store/offline/offlineMiddleware";
import {TodoistApi} from "./todoist/domain/TodoistApi";
import {todoistApi} from "./todoist/infrastructure/HttpTodoistApi";

export interface Injections {
    todoistApi: TodoistApi
}

const activeApiKey = (): ApiKey => {
    const apiKey = store.getState().profiles.find(it => it.active)?.value;
    if (!apiKey) {
        throw Error("Missing active API Key");
    }
    return apiKey
}

export const DAY_LIMIT = "20:00";

export interface RescheduleDayPayload {
    taskIds: TaskId[],
    dueDate: DueDate
}

export interface RescheduleTaskPayload {
    taskId: TaskId,
    period: Period
}

export interface UnscheduleTaskPayload {
    taskId: TaskId,
}

export interface ChangeProfilePayload {
    id: ProfileId,
    change: {
        color?: Hex
    }
}

export interface AddProfilePayload {
    id: ProfileId,
    name: string,
    value: ApiKey,
    color?: Hex

}

export interface AssignEstimationPayload {
    taskId: TaskId,
    label: LabelName,
}

export interface DeleteTaskPayload {
    taskId: TaskId
}

function syncingWhile(operation: () => Promise<any>, actions: Actions<StoreModel>) {
    let id = randomUUID();
    actions.startSyncing(id);
    operation()
        .finally(() => actions.stopSyncing(id))
}

export interface StoreModel {
    syncing: UUID[];
    startSyncing: Action<StoreModel, UUID>;
    stopSyncing: Action<StoreModel, UUID>;

    profiles: Profile[]
    addProfile: Action<StoreModel, AddProfilePayload>;
    addProfileWithKey: Action<StoreModel, { value: ApiKey, }>;
    changeProfile: Action<StoreModel, ChangeProfilePayload>;
    enableProfile: Action<StoreModel, { id: ProfileId }>;

    tasks: Tasks;

    createNewTask: Thunk<StoreModel, {
        content: string,
        estimation?: LabelName,
        dueDate?: DueDate,
        parentId?: TaskId
    }, Injections>
    deleteTask: Action<StoreModel, DeleteTaskPayload>;
    onDeleteTask: ThunkOn<StoreModel, Injections>;
    addTask: Action<StoreModel, { task: RawTask, rootParentId?: TaskId }>;
    overview: Computed<this, Record<Day, DayOverview>>;
    processingTasks: boolean;
    setProcessingTasks: Action<this, boolean>;
    labels: Record<LabelName, Label>;
    timeLabels: Computed<this, Label[]>;
    toEstimate: Computed<this, Task[]>;
    completeTask: Thunk<StoreModel, TaskId, Injections>;
    setTasks: Action<StoreModel, RawTask[]>;
    replaceLabels: Action<StoreModel, { taskId: TaskId, labels: LabelName[] }>;
    replacePriority: Action<StoreModel, { taskId: TaskId, priority: Priority }>;
    replaceDueDate: Action<StoreModel, { taskIds: TaskId[], dueDate: DueDate }>;
    removeTask: Action<StoreModel, TaskId>;
    setLabels: Action<StoreModel, Label[]>;
    addLabels: Action<StoreModel, Label[]>;
    fetchTasksAndLabels: Thunk<StoreModel, undefined, Injections>;
    addLabel: Thunk<StoreModel, { label: LabelName, taskId: TaskId }, Injections>;
    estimate: Thunk<StoreModel, { label: LabelName, taskId: TaskId }, Injections>;
    rescheduleTasks: Thunk<StoreModel, RescheduleDayPayload, Injections>;
    changePriority: Action<StoreModel, { priority: Priority, taskId: TaskId }>;
    onChangePriority: ThunkOn<StoreModel, Injections>;
    rescheduleTask: Thunk<StoreModel, RescheduleTaskPayload, Injections>;
    rescheduleTask2: Action<StoreModel, RescheduleTaskPayload>;
    onRescheduleTask: ThunkOn<StoreModel, Injections>;
    unscheduleTask: Action<StoreModel, UnscheduleTaskPayload>;
    onUnscheduleTask: ThunkOn<StoreModel, Injections>;

    assignEstimation: Action<StoreModel, AssignEstimationPayload>;
    onAssignEstimation: ThunkOn<StoreModel, Injections>;

    readyToDos: Computed<this, DoableTask[]>;

    _inProgressTask?: InProgressTask;
    inProgressTask: Computed<this, InProgressTask | undefined>;

    startWorkingOnTask: Action<StoreModel, TaskId>;
    stopWorkingOnTask: Action<StoreModel, TaskId>;

    indicator: Computed<this, IndicatorStore | undefined>;
    workCapacity: Computed<this, Minutes>;
}

function indicator(severity: Severity, area: Area, alwaysVisible?: boolean): IndicatorStore {
    return {severity, area, alwaysVisible};
}

async function createMandatoryLabels(labels: Label[], todoistApi: TodoistApi) {
    const labelNames = labels.map(label => label.name);
    return await Promise.all(Object.values(LABELS_DEFINITION).flatMap(it => it).filter(({name}) => !labelNames.includes(name))
        .map(({name, color}) => (todoistApi.createLabel({name, color}))));
}

export const storeModel: StoreModel = {
    tasks: {},
    labels: {},
    syncing: [],
    profiles: [],

    deleteTask: action((state, {taskId}) => {
        const parentId = state.tasks[taskId].parentId;
        delete state.tasks[taskId];
        if (parentId && !Object.values(state.tasks).some(task => task.parentId === parentId)) {
            state.tasks[parentId].category = TaskCategory.LEAF;
        }
    }),

    onDeleteTask: thunkOn(actions => actions.deleteTask, async (actions, {payload: {taskId}}, {injections: {todoistApi}}) => {
            const syncId = randomUUID();
            actions.startSyncing(syncId)
            return todoistApi.deleteTask(taskId).finally(() => actions.stopSyncing(syncId));
        }
    ),

    addProfile: action((state, {id, name, value, color}) => {
            state.profiles.push({
                id,
                name,
                value,
                color,
                active: state.profiles.length === 0
            })
        }
    ),

    addProfileWithKey: action((state, {value: apiKey}) => {
            const exists = state.profiles.some(({value}) => value === apiKey);
            if (!exists) {
                state.profiles.push({
                    id: nextProfileId(),
                    name: `Profile # ${state.profiles.length + 1}`,
                    value: apiKey,
                    active: state.profiles.length === 0
                })

            }
        }
    ),

    changeProfile: action((state, {id, change: {color}}) => {
            state.profiles = state.profiles.map(it => it.id === id ? {...it, color} : it)
        }
    ),

    enableProfile: action((state, {id}) => {
            state.profiles.forEach(it => it.active = it.id === id)
        }
    ),

    startSyncing: action((state, uuid) => {
            if (!state.syncing.includes(uuid)) {
                state.syncing.push(uuid)
            }
        }
    ),
    stopSyncing: action((state, uuid) => {
            state.syncing = state.syncing.filter(it => it !== uuid)
        }
    ),

    processingTasks: false,
    setProcessingTasks: action((state, payload) => {
            state.processingTasks = payload;
        }
    ),
    timeLabels: computed((state) =>
        Object.values(state.labels)
            .filter(label => label.category === LabelCategory.ESTIMATION)
            .sort((a, b) => a.order - b.order)
    ),
    toEstimate: computed((state) =>
        findEstimableFrom(Object.values(state.tasks), clock.today())
            .sort(sortTasks)
    ),
    addTask: action((state, {task, rootParentId}) => {
        let tasks = dfsTree([...Object.values(state.tasks), task], rootParentId ? rootParentId : task.id);
        tasks.forEach((t) => {
            state.tasks[t.id] = t
        })
    }),
    createNewTask: thunk(async (actions, {content, dueDate, parentId, estimation}, {
            getState,
            injections: {todoistApi}
        }) => {
            function findRootParentId(parentId?: TaskId): TaskId | undefined {
                const allTasks = Object.values(getState().tasks);
                const rootId = allTasks.find(task => task.id === parentId)?.rootId;
                return rootId ? allTasks.find(and(belongsTo(rootId), isRoot))?.id : undefined;
            }

            function isScheduled(taskId?: TaskId): boolean {
                if (!taskId) {
                    return false;
                }
                return !!getState().tasks[taskId].dueDate
            }

            const rootParentId = findRootParentId(parentId);

            const taskResponse = await todoistApi.createTask({
                content,
                due_date: dueDate,
                parent_id: parentId,
                labels: estimation ? [estimation] : undefined
            }).then(response => response.body);
            let task = asTaskFromApi(taskResponse);
            actions.addTask({task: task, rootParentId: rootParentId})

            if (isScheduled(parentId)) {
                actions.unscheduleTask({taskId: parentId!});
            }
        }
    ),
    setTasks: action((state, tasks) => {
        state.tasks = asRecord(dfs(tasks.filter(task => !task.completed)))
    }),
    overview: computed((state) => {
        return Object.values(state.readyToDos).reduce((prev, curr) => {
                let dueDate = curr.dueDate;
                let dayOverview: DayOverview = prev[dueDate!] || {tasksAmount: 0};
                dayOverview.tasksAmount++;
                prev[dueDate!] = dayOverview
                return prev;
            }
            , {} as Record<Day, DayOverview>);
    }),

    replaceLabels: action((state, {taskId, labels}) => {
        let task = state.tasks[taskId];
        task.labels = labels;
        task.estimation = tryExtractEstimationFrom(labels)
    }),
    replaceDueDate: action((state, {taskIds, dueDate}) => {
        taskIds.forEach(taskId => {
            let task = state.tasks[taskId];
            task.dueDate = dueDate;
        })
    }),
    replacePriority: action((state, {taskId, priority}) => {
        state.tasks[taskId].priority = priority;
    }),
    removeTask: action((state, taskId) => {
        delete state.tasks[taskId]
    }),
    setLabels: action((state, labels) => {
        state.labels = asRecordLabels(labels);
    }),
    addLabels: action((state, labels) => {
        state.labels = {...state.labels, ...asRecordLabels(labels)};
    }),
    fetchTasksAndLabels: thunk(async (actions, _, {injections: {todoistApi}}) => {
            syncingWhile(async () => {
                let projects = await todoistApi.getProjects();
                let inboxProjectId = projects.find(project => project.is_inbox_project)!.id;

                const [tasksResponse, labelsResponse] = await Promise.all([
                    todoistApi.getTasks(),
                    todoistApi.getLabels(),
                ]);

                let receivedTasks = tasksResponse.body;
                const labels = asLabels(labelsResponse);

                const createdLabels = await createMandatoryLabels(labels, todoistApi);
                actions.addLabels(asLabels(createdLabels))

                let tasks = asTasksFromApi(receivedTasks.filter(task => task.project_id === inboxProjectId));
                actions.setTasks(tasks);
                if (createdLabels.length === 0) {
                    actions.setLabels(labels);
                } else {
                    actions.addLabels(labels);
                }
            }, actions)

        }
    ),

    completeTask: thunk(async (actions, taskId, {injections: {todoistApi}}) => {
            actions.setProcessingTasks(true);
            try {
                await todoistApi.closeTask(taskId);
                actions.removeTask(taskId)
            } finally {
                actions.setProcessingTasks(false);
            }

        }
    ),
    addLabel: thunk(async (actions, {label, taskId}, {getState, injections: {todoistApi}}) => {
            let task = getState().tasks[taskId]
            if (task) {
                actions.setProcessingTasks(true);
                let labels = [label, ...task.labels];
                try {
                    let taskId = task.id;
                    let response = await todoistApi.replaceLabels(taskId, labels);
                    if (response.successful) {
                        actions.replaceLabels({
                            taskId,
                            labels
                        })
                    }

                } finally {
                    actions.setProcessingTasks(false);
                }

            }
        }
    ),

    estimate: thunk(async (actions, {label, taskId}, {getState, injections: {todoistApi}}) => {
            let task = getState().tasks[taskId]
            if (task && isTimeLabel(label)) {
                actions.setProcessingTasks(true);
                let labels = [label, ...task.labels.filter(l => !isTimeLabel(l))];
                try {
                    let taskId = task.id;
                    let response = await todoistApi.replaceLabels(taskId, labels);
                    if (response.successful) {
                        actions.replaceLabels({
                            taskId,
                            labels
                        })
                    }
                } finally {
                    actions.setProcessingTasks(false);
                }
            }
        }
    ),

    changePriority: action((state, {taskId, priority}) => {
        state.tasks[taskId].priority = priority;
    }),

    onChangePriority: thunkOn(
        actions => actions.changePriority, (
            async (actions, {payload}, {injections: {todoistApi}}) => {
                const syncId = randomUUID();
                actions.startSyncing(syncId)
                const {priority, taskId} = payload;
                return todoistApi.updateTask(taskId, {
                    priority: priority
                }).finally(() => actions.stopSyncing(syncId));
            }
        )
    ),


    assignEstimation: action((state, {taskId, label}) => {
        assignEstimation(state.tasks[taskId], label, state.labels)
    }),
    onAssignEstimation: thunkOn(actions => actions.assignEstimation, (async (actions, {payload: {taskId}}, {
        getState,
        injections: {todoistApi}
    }) => {
        const syncId = randomUUID();
        actions.startSyncing(syncId)
        const labels = getState().tasks[taskId].labels
        return todoistApi.updateTask(taskId, {
            labels
        }).finally(() => actions.stopSyncing(syncId));
    })),

    unscheduleTask: action((state, {taskId}) => {
            state.tasks[taskId].dueDate = undefined;
        }
    ),

    onUnscheduleTask: thunkOn(
        actions => actions.unscheduleTask, (
            async (actions, {payload: {taskId}}, {injections: {todoistApi}}) => {
                const syncId = randomUUID();
                actions.startSyncing(syncId)
                return todoistApi.updateTask(taskId, {
                    due_string: "no date"
                }).finally(() => actions.stopSyncing(syncId));
            }
        )
    ),

    rescheduleTask2: action((state, {period: {amount, unit}, taskId}) => {
            const newDueDate = dueDateFrom(dayjs().add(amount, unit));
            state.tasks[taskId].dueDate = newDueDate;
        }
    ),

    onRescheduleTask: thunkOn(
        actions => actions.rescheduleTask2, (
            async (actions, {payload}, {injections: {todoistApi}, getState}) => {
                const syncId = randomUUID();
                actions.startSyncing(syncId)
                const {taskId} = payload;
                const newDueDate = getState().tasks[taskId].dueDate
                return todoistApi.updateTask(taskId, {
                    due_date: newDueDate
                }).finally(() => actions.stopSyncing(syncId));
            }
        )
    ),

    rescheduleTask: thunk(async (actions, {period: {amount, unit}, taskId}, {injections: {todoistApi}}) => {
            actions.setProcessingTasks(true);
            try {
                const newDueDate = dueDateFrom(dayjs().add(amount, unit));
                let response = await todoistApi.updateTask(taskId, {
                    due_date: newDueDate
                });

                if (response.successful) {
                    actions.replaceDueDate({taskIds: [taskId], dueDate: newDueDate})
                }
            } finally {
                actions.setProcessingTasks(false);
            }
        }
    ),
    rescheduleTasks: thunk(async (actions, {taskIds, dueDate}, {injections: {todoistApi}}) => {
            actions.setProcessingTasks(true);
            try {
                let response = await todoistApi.updateDueDateTasks(taskIds, dueDate);

                if (response.successful) {
                    actions.replaceDueDate({taskIds, dueDate: dueDate})
                }
            } finally {
                actions.setProcessingTasks(false);
            }

        }
    ),
    readyToDos: computed((state) =>
        Object.values(state.tasks)
            .filter(and(estimated, scheduled, leaf))
            .map(task => asDoableTask(task, clock.today()))
            .sort(sortTasks)
    ),

    workCapacity: computed((state) =>
        state.readyToDos.reduce((acc, task) => {
            acc += task.estimation.duration
            return acc;
        }, 0)
    ),

    startWorkingOnTask: action((state, taskId) => {
        const inProgressTask: InProgressTask = {
            id: taskId,
            startedAt: clock.now()
        };
        state._inProgressTask = inProgressTask
    }),

    stopWorkingOnTask: action((state, taskId) => {
        state._inProgressTask = undefined;
    }),

    indicator: computed((state) => {
            if (state.inProgressTask) {
                return indicator(Severity.URGENT, Area.WORK)
            }
            if (state.toEstimate.length > 0) {
                return indicator(Severity.BASIC, Area.ESTIMATION)
            }

            if (state.workCapacity > workCapacityLeft(DAY_LIMIT)) {
                return indicator(Severity.WARN, Area.WORK, true)
            }

            if (state.readyToDos.length > 0) {
                return indicator(Severity.INFO, Area.WORK)
            }
        }
    ),

    inProgressTask: computed((state) => {
            if (state._inProgressTask && state.tasks[state._inProgressTask.id]) {
                return state._inProgressTask
            }
            return undefined;
        }
    )
};

const injections: Injections = {
    todoistApi: todoistApi(activeApiKey)
}

const store = createStore(persist(storeModel, {storage: "localStorage", deny: ["syncing"]}),
    {middleware: [offlineMiddleware], injections: injections});

export default store;

const typedHooks = createTypedHooks<StoreModel>();

export const useStoreActions = typedHooks.useStoreActions;
export const useStoreDispatch = typedHooks.useStoreDispatch;
export const useStoreState = typedHooks.useStoreState;
