import dayjs from "./dayjs";
import {Day} from "../domain/Day";
import {parseDayJsFrom} from "../todoist/DueDate";

export interface HumanizedDay {
    dayDiff: number;
    text: string;
    dayOfWeek: string;
}

function extractDayOfWeek(date: dayjs.Dayjs): string {
    return date.format("ddd")
}

export const humanizeDueDay = (today: Day, value: Day): HumanizedDay => {
    let parsedValue = parse(value);
    let parsedToday = parse(today);
    let dayOfWeek = extractDayOfWeek(parsedValue)
    let diff = parsedValue.diff(parsedToday, "days");
    switch (diff) {
        case 0:
            return {dayDiff: diff, text: "Today", dayOfWeek};
        case 1:
            return {dayDiff: diff, text: "Tomorrow", dayOfWeek};
        case -1:
            return {dayDiff: diff, text: "Yesterday", dayOfWeek};
        default:
            return {dayDiff: diff, text: dayjs.duration(parsedValue.diff(parsedToday)).humanize(true), dayOfWeek};
    }
}

function parse(value: Day) {
    return parseDayJsFrom(value)
}
