import React, {ReactElement, useState} from 'react';
import {PropsWithChildren} from "../shared/PropsWithChildren";
import styles from "./Expandable.module.css"

interface ExpandableProps{
    component: ReactElement
}

function Expandable({children, component}: PropsWithChildren<ExpandableProps>) {
    const [expanded, setExpanded] = useState(false);

    const toggleExpand = () => {
        setExpanded(!expanded);
    };

     return (
        <>
            {expanded && <div className={styles.Overlay} onClick={toggleExpand}></div>}
            <div className={styles.ExpandableContainer}>
                <div className={styles.ExpandableHeader} onClick={toggleExpand}>
                    {component}
                </div>
                {expanded && (
                    <div className={styles.ExpandableContent} onClick={toggleExpand}>
                        {children}
                    </div>
                )}
            </div>
        </>
    );;
}

export default Expandable;
