import {Day} from "../domain/Day";
import {useParams} from "react-router-dom";
import {clock} from "../domain/Clock";
import {DayParam} from "./DayParam";

export function usePlanParam(): { day: Day } {
    let {day: dayParam} = useParams<{ day: DayParam }>();
    if (dayParam === "today") {
        return {day:  clock.today()};
    } else {
        return {day: dayParam as Day}
    }
}
