export function entries<K extends string | symbol | number, V>(obj: Record<K, V>): [K, V][] {
    return Object.entries(obj).map(([key, value]) => {
        if (isNumber(key)) {
            return [Number(key) as K, value as V];
        }
        return [key as K, value as V]
    });
}


function isNumber(key: string | number | symbol): boolean {
    return !isNaN(Number(key));
}
