import {byPriority, leaf, notEstimated, scheduledAtLestFor, Task} from "../domain/Task";
import {DueDate} from "../todoist/DueDate";
import {and} from "../shared/and";
import {RootId} from "../shared/RootId";
import {TaskId} from "../shared/TaskId";
import {by} from "../shared/by";

export function findEstimableFrom(tasks: Task[], today: DueDate): Task[] {

    const firstTaskPreRoot: Record<RootId, TaskId> = {};

    const leafs = tasks.filter(leaf).sort(by(sortEstimatedFirst, byPriority))

    leafs.reduce((acc, current) => {
        const rootId = current.rootId;
        if (!acc[rootId]) {
            acc[rootId] = current.id
        }
        return acc;
    }, firstTaskPreRoot)

    return leafs.filter(and(scheduledAtLestFor(today), notEstimated, foundIn(firstTaskPreRoot)));
}

function sortEstimatedFirst(a: Task, b: Task) {
    if (a.estimation && !b.estimation) {
        return -1;
    }
    if (!a.estimation && b.estimation) {
        return 1;
    }
    return 0;
}


function foundIn(firstTaskPreRoot: Record<RootId, TaskId>): (task: Task) => boolean {
    return function ({id, rootId}: Task): boolean {
        return firstTaskPreRoot[rootId] === id;
    }
}
