import React from 'react';
import {TaskId} from "../shared/TaskId";
import {Priority} from "../shared/Priority";
import {priorityColor} from "../shared/priorityColor";
import {entries} from "../shared/entries";
import {useStoreActions} from "../store";
import styles from "./ChangePriority.module.css"
import useIsMobile from "../shared/components/useMinSize";
import SubActions, {Shortcut} from "../shared/components/sub-actions/SubActions";
import SubAction from "../shared/components/sub-actions/SubAction";
import Icon from "../shared/Icon";

interface ChangePriorityProps {
    taskId: TaskId;
    currentPriority: Priority;
}

const priorities: Record<Priority, Shortcut> = {
    [Priority.BASIC]: "Q",
    [Priority.LOW]: "W",
    [Priority.MEDIUM]: "E",
    [Priority.HIGH]: "R",
}

function ChangePriority({taskId, currentPriority}: ChangePriorityProps) {

    const changePriority = useStoreActions(actions => actions.changePriority)

    const isMobile = useIsMobile();

    const performChangePriority = (priority: Priority) => {
        changePriority({taskId, priority})
    }
    return <div className={styles.ChangePriority}>
        <SubActions shortcut="P" label={<Icon name="flag.svg"/>}>
            {entries(priorities).map(([key, value]) =>
                <SubAction key={key}
                           shortcut={value}
                           color={priorityColor(key)}
                           label={isMobile ? Priority[key].charAt(0) : Priority[key]}
                           disabled={currentPriority === key}
                           onClick={() => {
                               performChangePriority(key);
                           }}/>)
            }

        </SubActions>
    </div>
}

export default ChangePriority;
