import React from 'react';
import {DueDate} from "../todoist/DueDate";
import {humanizeDueDay} from "../shared/humanizeDueDay";
import styles from "./DueDateItem.module.css"
import {classNames} from "../shared/components/className";
import {Dots} from "./Dot";
import {ColorKey, getColorByKey, Hex} from "../shared/Color";

interface DueDateProps {
    date?: DueDate;
    today: DueDate;
}

function DueDateItem({date, today}: DueDateProps) {
    if (!date) {
        return <div className={styles.DueDate}>&nbsp;</div>
    }
    const {text, dayDiff} = humanizeDueDay(today, date);

    const colorFor = (dayDiff: number): Hex=> {
        if (dayDiff < 0) {
            return getColorByKey(ColorKey.RED);
        }
        if (dayDiff === 0) {
            return getColorByKey(ColorKey.ORANGE);
        }
        if (dayDiff === 1) {
            return getColorByKey(ColorKey.GREEN);
        }
        if (dayDiff === 2) {
            return getColorByKey(ColorKey.LIME_GREEN);
        }
        if (dayDiff === 3) {
            return getColorByKey(ColorKey.SKY_BLUE);
        }
        return getColorByKey(ColorKey.GREY);
    }

    const color = colorFor(dayDiff)

    return <div className={classNames(
        styles.DueDate,
    )}>
        {dayDiff > 0 && <Dots value={dayDiff} direction="right" color={color}/>}
        <div className={styles.Primary} style={{color: color}}>{text}</div>
        {dayDiff < 0 && <Dots value={dayDiff} direction="left" color={color}/>}

    </div>;
}

export default DueDateItem;
