import {Task} from "./domain/Task";
import {LabelName} from "./shared/LabelName";
import {Label, LabelCategory} from "./domain/Label";
import {extractMinutesFrom} from "./todoist/asLabels";

export function assignEstimation(task: Task, label: LabelName, labels: Record<LabelName, Label>) {
    const withoutEstimations = task.labels.filter(label => labels[label].category !== LabelCategory.ESTIMATION)
    task.labels = [...withoutEstimations, label];
    task.estimation = {
        code: label,
        duration: extractMinutesFrom(label)
    }
}
