import {belongsTo, byDueDate, byOrder, byPriority, Task} from "../domain/Task";
import {RootId} from "../shared/RootId";
import {and} from "../shared/and";
import {isRoot} from "../domain/RawTask";
import {by} from "../shared/by";

export interface Project extends Task {
    subtasks: Project[]
}

export function projectFrom(tasks: Task[], rootId: RootId): Project {
    const root = tasks.find(and(belongsTo(rootId), isRoot))!;
    return buildProjectFrom(root, tasks)
}

function buildProjectFrom(parent: Task, tasks: Task[]): Project {
    const children = tasks.filter(({parentId}) => parentId === parent.id)
    return {
        ...parent,
        subtasks: children.map(child => buildProjectFrom(child, tasks))
            .sort(by(byDueDate, byPriority, byOrder))
    };
}

