import Svg, {SvgSize} from "./Svg";
import {Hex} from "./Color";

interface IconProps {
    size?: SvgSize
    name: string;
    onClick?: ()=> void;
    className?: string;
    color?: Hex
}

function Icon({name, size = 16, onClick, className, color}: IconProps) {
    return <Svg size={size} src={`/assets/icons/${name}`} onClick={onClick} className={className} style={{color: color}}/>
}

export default Icon
