import dayjs from "../shared/dayjs";

export type DueDate = `${number}${number}${number}${number}-${number}${number}-${number}${number}`

const DUE_DATE_REGEX = /^\d{4}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01])$/

export function dueDateFrom(dayjs: dayjs.Dayjs): DueDate {
    const output = dayjs.format("YYYY-MM-DD");
    if (!DUE_DATE_REGEX.test(output)) {
        throw new Error(`Created output: ${output} isn't valid format for Day`)
    }
    return output as DueDate
}

export function parseDayJsFrom(day: DueDate): dayjs.Dayjs {
    return dayjs(day).startOf("day");
}
