import {createContext, useContext} from "react";
import {HotKeyScope} from "../../hotkeys/HotKeyScope";

interface ContextScopeType {
    scope: HotKeyScope,
    onActionCompleted?: () => void;
}

export const ScopeContext = createContext<ContextScopeType>({scope: "default"});

export const useContextScope = () => {
    return useContext(ScopeContext);
};
