import {occurrence} from "./occurrence";
import {clock} from "../domain/Clock";
import styles from "./EstimateTask.module.css"
import {decrease, increase, Priority} from "../shared/Priority";
import Progress from "./Progress";
import Svg from "../shared/Svg";
import {useStoreActions, useStoreState} from "../store";
import React, {useCallback, useEffect, useState} from "react";
import debounce from "lodash/debounce";
import {TaskId} from "../shared/TaskId";
import PriorityIcon from "../shared/components/PriorityIcon";
import Path from "../shared/components/Path";
import {Task} from "../domain/Task";
import {useHotkeys} from "../shared/hotkeys/useHotkeys";

interface EstimateTaskProps {
    task: Task,
    tasks: Task[],
    currentId: TaskId,
}

export function EstimateTask({currentId, tasks}: EstimateTaskProps) {

    const task = tasks.find(it => it.id === currentId)!;

    let changePriority = useStoreActions(actions => actions.changePriority);
    let processingTask = useStoreState(state => state.processingTasks);
    let [priority, setPriority] = useState<Priority>(task.priority)

    useEffect(() => {
        debouncedChangePriority({taskId: task.id, priority})
    }, [priority])

    useEffect(() => {
        setPriority(task.priority)
    }, [task.priority])

    function performChangePriority({taskId, priority}: { taskId: TaskId, priority: Priority }) {
        if (taskId === task.id) {
            changePriority({
                taskId: taskId,
                priority: priority
            });
        }
    }

    const debouncedChangePriority = useCallback(debounce(performChangePriority, 500), [])


    useHotkeys("up", async () => {
        setPriority(increase(priority))
    }, {enabled: !processingTask})

    useHotkeys("down", async () => {
        setPriority(decrease(priority))
    }, {enabled: !processingTask})


    return <div>
        <Progress selectedId={currentId} taskIds={tasks.map(it => it.id)}/>
        <div className={styles.Header}>
            <PriorityIcon value={priority}/>
            <h1 className={styles.Priority}>{task.content}</h1>
        </div>
        <Path path={task.path}/>
        <div className={styles.Labels}>
            <div style={{visibility: task.labels.length === 0 ? "hidden" : "visible"}}>
                <Svg size={16} src={"/assets/icons/tag.svg"}/>
            </div>
            {task.labels.map(label =>
                <div key={label} className={styles.Label}>
                    {label}
                </div>)}
        </div>
        <div className={styles.Occurrence}>
            {occurrence(task.dueDate!, clock.today())}
        </div>
    </div>
}
