import dayjs from "../shared/dayjs";
import {DayOverview} from "./DayOverview";
import Progress from "./Progress";
import styles from "./DayHeader.module.css"
import {classNameIf, classNames} from "../shared/components/className";
import usePlanNavigation from "./planNavigate";
import {Day} from "../domain/Day";
import {usePlanParam} from "./usePlanParam";

interface DayHeaderProps {
    value: Day
    today: Day
    dayOverview?: DayOverview
}

function parse(value: Day) {
    return dayjs(value).startOf("day");
}


function DayHeader({value, today, dayOverview}: DayHeaderProps) {
    const {day} = usePlanParam();
    const {planFor} = usePlanNavigation();

    const selected = value === day

    const humanize = (): { dayDiff: number, text: string } => {
        let parsedValue = parse(value);
        let parsedToday = parse(today);
        let diff = parsedValue.diff(parsedToday, "days");
        switch (diff) {
            case 0:
                return {dayDiff: diff, text: "Today"};
            case 1:
                return {dayDiff: diff, text: "Tomorrow"};
            case -1:
                return {dayDiff: diff, text: "Yesterday"};
            default:
                return {dayDiff: diff, text: parsedValue.format("ddd (DD)")};
        }
    }

    function performNavigate() {
        planFor(value)
    }

    return <div className={classNames(styles.DayHeader, classNameIf(selected, styles.Selected))}
                onClick={performNavigate}>
        {humanize().text}
        {dayOverview && <Progress value={dayOverview.tasksAmount}/>}
    </div>


}

export default DayHeader
