import React from 'react';
import {classNameIf, classNames} from "../shared/components/className";
import styles from "./DurationBased.module.css"
import {Priority} from "../shared/Priority";
import {priorityColor} from "../shared/priorityColor";

interface PriorityItemProps {
    priority: Priority;
    marked?: boolean;
    overdue?: boolean;
}

function PriorityItem({priority, marked, overdue = false}: PriorityItemProps) {
    const color = priorityColor(priority);
    return <div
        className={classNames(styles.DurationBased, classNameIf(overdue, styles.Overdue))}
        style={{
            flexShrink: 0,
            borderColor: color,
            backgroundColor: marked ? color : "transparent",
        }}/>
}

export default PriorityItem;
