import {LabelResponse} from "./LabelResponse";
import {getColorObjByKey} from "../shared/Color";
import {LabelName} from "../shared/LabelName";
import {Minutes} from "../shared/Minutes";
import {Label, LabelCategory} from "../domain/Label";

export const timeLabelRegex = /^\d+[mhd]$/;

type CategorizedLabel = Omit<Label, "order">
type LabelsForEachCategory = Record<LabelCategory, CategorizedLabel[]>

type SortOperation = (a: CategorizedLabel, b: CategorizedLabel) => number;
type MatchOperation = (name: LabelName) => boolean;

interface LabelOperations {
    sort: SortOperation,
    match: MatchOperation
}

const labelOperations = (sort: SortOperation, match: MatchOperation): LabelOperations => ({sort, match})


const sortByName: SortOperation = (a: CategorizedLabel, b: CategorizedLabel) => a.name.localeCompare(b.name)

const matchByRegex = (regex: RegExp): MatchOperation => (name: LabelName) => regex.test(name);

const matchAnyOf = (allowed: LabelName[]): MatchOperation => (name: LabelName) => allowed.includes(name);

const any: MatchOperation = (_: LabelName) => true;
const none: MatchOperation = (_: LabelName) => false;

const sortByDurationFromName: SortOperation = (a: CategorizedLabel, b: CategorizedLabel) => (extractMinutesFrom(a.name) - extractMinutesFrom(b.name));

export const LABEL_TYPES: Record<LabelCategory, LabelOperations> = {
    [LabelCategory.ESTIMATION]: labelOperations(sortByDurationFromName, matchByRegex(timeLabelRegex)),
    [LabelCategory.PROJECT]: labelOperations(sortByName, matchAnyOf(["root"])),
    [LabelCategory.EXECUTION]: labelOperations(sortByName, none),
    [LabelCategory.PRIORITIZATION]: labelOperations(sortByName, none),
    [LabelCategory.VALUE]: labelOperations(sortByName, none),
    [LabelCategory.BASIC]: labelOperations(sortByName, any),
}

const createLabelsForEachCategory = (): LabelsForEachCategory => (
    {
        [LabelCategory.BASIC]: [],
        [LabelCategory.ESTIMATION]: [],
        [LabelCategory.EXECUTION]: [],
        [LabelCategory.PROJECT]: [],
        [LabelCategory.VALUE]: [],
        [LabelCategory.PRIORITIZATION]: []
    });


export function asLabels(input: LabelResponse[]): Label[] {
    const unorderedLabels = input.map(asCategorizedLabel);
    const groupedByCategory = unorderedLabels.reduce<LabelsForEachCategory>((prev, curr) => {
        prev[curr.category].push(curr)
        return prev;
    }, createLabelsForEachCategory());
    return Object.entries(groupedByCategory).map(([key, value]) => {
        const enumKey = key as LabelCategory
        const labelSortFn = LABEL_TYPES[enumKey].sort;
        return value.sort(labelSortFn).map((it, index) =>
            ({...it, order: index}));
    }).flatMap(it => it)
}

export function asCategorizedLabel(item: LabelResponse): CategorizedLabel {
    let category = Object.entries(LABEL_TYPES)
        .map(([key, value]) => ([key as LabelCategory, value]))
        .filter(([_, val]) => val.match(item.name))
        .map(([key])=> (key as LabelCategory))
        .first();

    if (!category) {
        throw new Error("Cant determine category for item: " + JSON.stringify(item))
    }

    return ({
        name: item.name,
        color: getColorObjByKey(item.color),
        category: category
    });
}

export function isTimeLabel(name: LabelName): boolean {
    return LABEL_TYPES[LabelCategory.ESTIMATION].match(name)
}

export function extractMinutesFrom(name: LabelName): Minutes {
    if (name.endsWith("m")) {
        return parseInt(name.slice(0, -1));
    }
    if (name.endsWith("h")) {
        return parseInt(name.slice(0, -1)) * 60;
    }

    if (name.endsWith("d")) {
        return parseInt(name.slice(0, -1)) * 60 * 24;
    }

    throw new Error(`Can't extract minutes from label: '${name}'`)
}
