import {isRoot, RawTask} from "../domain/RawTask";
import {TaskId} from "../shared/TaskId";
import {rootIdFrom} from "../shared/RootId";
import {Task, TaskCategory} from "../domain/Task";


export function dfs(nodes: RawTask[]): Task[] {
    return nodes.filter(isRoot).map(({id}) => dfsTree(nodes, id)).flatMap(it => it)
}

export function dfsTree(nodes: RawTask[], currentNodeId: TaskId, parentPath: TaskId[] = []): Task[] {
    const currentNode = nodes.find(node => node.id === currentNodeId);

    if (!currentNode) {
        throw new Error(`Node with id ${currentNodeId} not found`);
    }

    const isRoot = !currentNode.parentId;

    const childNodes = nodes.filter(node => node.parentId === currentNodeId && node.id !== currentNodeId);

    const category: TaskCategory = childNodes.length === 0 ? TaskCategory.LEAF : TaskCategory.NODE;

    let currentPath: TaskId []
    if (isRoot) {
        currentPath = []
    } else {
        currentPath = [...parentPath, currentNode.parentId!]
    }
    const rootId = rootIdFrom(isRoot ? currentNode.id : currentPath[0]);

    const resultNode: Task = {
        ...currentNode,
        rootId: rootId,
        path: currentPath,
        category,
    };

    const childResults = childNodes.flatMap(child => dfsTree(nodes, child.id, currentPath));

    return [resultNode, ...childResults];
}
