import React from 'react';
import {Estimation} from "../../domain/RawTask";
import styles from "./DurationComp.module.css"
import range from "../../shared/range";
import {useStoreState} from "../../store";
import {classNameIf, classNames} from "../../shared/components/className";

interface DurationCompProps {
    estimation: Estimation;
    colored?: boolean;
}

function DurationComp({estimation, colored = true}: DurationCompProps) {

    const labels = useStoreState(state => state.labels);

    const hoursMode = estimation.duration >= 60;

    const divider = hoursMode ? 60 : 5;

    const color = colored ? labels[estimation.code]?.color?.value : undefined;

    const itemsAmount = estimation.duration / divider;


    return <div className={styles.DurationComp} style={{opacity: !colored ? .4 : undefined}}>
        <div className={styles.Value}
             style={{color: colored ? color : undefined}}>{estimation.code}</div>
        {range(itemsAmount).map(key =>
            <div key={key} style={{backgroundColor: color}}
                 className={classNames(styles.Item, classNameIf(hoursMode, styles.Hours))}/>)}
    </div>;
}

export default DurationComp;
