import styles from "./Hotkey.module.css"
import Skeleton from "../components/Skeleton";
import React from "react";
import {Hex} from "../Color";
import {classNameIf, classNames} from "../components/className";
import {MAIN_TEXT_COLOR} from "../theme";
import {styleIf} from "../components/styleIf";
import {HotKeyScope} from "./HotKeyScope";
import {shortcutFrom} from "./shortcutFrom";
import {useHotkeys} from "./useHotkeys";


interface HotkeyProps {
    binding: string,
    label: string | React.ReactNode,
    shortcut?: string | React.ReactNode,
    onClick: () => void
    color?: Hex,
    revert?: boolean
    disabled?: boolean
    loading?: boolean
    glow?: boolean,
    scope?: HotKeyScope
}

export function HotKey({
                           label,
                           binding,
                           shortcut = binding,
                           color = MAIN_TEXT_COLOR,
                           loading,
                           disabled = false,
                           onClick,
                           revert = false,
                           glow = false,
                           scope = "default"
                       }: HotkeyProps) {
    useHotkeys(binding, () => {
        onClick()
    }, {enabled: !disabled && !loading, scope: scope})

    return <Skeleton enabled={!!loading}>
        <button className={classNames(
            styles.Hotkey,
            classNameIf(disabled, styles.Disabled),
            classNameIf(revert, styles.Revert)
        )}
                style={{borderColor: color, ...styleIf(glow, {boxShadow: `0 0 8px 4px ${color}`})}}
                onClick={onClick}>
            <div className={styles.Binding}
                 style={{backgroundColor: color}}>
                {shortcutFrom(shortcut)}
            </div>
            <div className={styles.Value} style={{color: color}}>
                {label}
            </div>
        </button>
    </Skeleton>


}
