import React from 'react';

import 'react-toastify/dist/ReactToastify.css';
import {Bounce, ToastContainer} from "react-toastify";


function Toasts() {
    return <>
        <ToastContainer
            position="top-right"
            autoClose={5000}
            stacked
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            pauseOnHover
            theme="colored"
            transition={Bounce}
        />
    </>;
}

export default Toasts;
