import dayjs from "../shared/dayjs";
import {Day} from "../domain/Day";

export function occurrence(value: Day, today: Day): string {

    function parse(value: Day) {
        return dayjs(value).startOf("day");
    }

    let parsedValue = parse(value);
    let parsedToday = parse(today);
    let diff = parsedValue.diff(parsedToday, "days");
    switch (diff) {
        case 0:
            return "today";
        case 1:
            return "tomorrow";
        case -1:
            return "yesterday";
        default:
            return dayjs.duration(parsedValue.diff(parsedToday)).humanize(true)
    }
}
