import {LabelName} from "../shared/LabelName";
import {timeLabelRegex} from "./asLabels";

export function getEstimationLabel(labels: LabelName[]): LabelName {
    const found = labels.find(label => timeLabelRegex.test(label));
    if (!found) {
        throw new Error(`Missing estimation in labels: ${JSON.stringify(labels)}`)
    }
    return found;
}
