import {Hex} from "../shared/Color";
import styles from "./ProgressItem.module.css"
import {classNameIf, classNames} from "../shared/components/className";

export interface ProgressItemProps {
    color?: Hex;
    active: boolean;
}

function ProgressItem({color, active}: ProgressItemProps) {
    return <div  className={classNames(styles.Item, classNameIf(active, styles.Active))}
                style={{borderColor: color, backgroundColor: active ? color : "transparent"}}/>
}

export default ProgressItem
