import {PropsWithChildren} from "../PropsWithChildren";
import styles from "./Selectable.module.css"
import {classNameIf, classNames} from "./className";

interface SelectableProps {
    selected?: boolean
}

function Selectable({selected = false, children}: PropsWithChildren<SelectableProps>) {
    return <div className={classNames(styles.Selectable, classNameIf(selected, styles.Selected))}>
        {children}
    </div>;
}

export default Selectable;
