import React from 'react';
import styles from "./Doable.module.css"
import {classNameIf, classNames} from "../shared/components/className";
import {Priority} from "../shared/Priority";
import {priorityColor} from "../shared/priorityColor";


interface DoableProps {
    selected: boolean;
    priority: Priority;
}

function Doable({selected, priority}: DoableProps) {
    return <div className={styles.Doable} style={{borderColor: priorityColor(priority)}}>
        <div className={classNames(styles.Inner, classNameIf(selected, styles.Selected))}
             style={{backgroundColor: selected ? priorityColor(priority): undefined}}/>
    </div>;
}

export default Doable;
