import {LabelCategory} from "../domain/Label";
import {LabelName} from "../shared/LabelName";
import {ColorKey} from "../shared/Color";
import {LABEL_TYPES} from "../todoist/asLabels";

interface LabelDefinition {
    name: LabelName;
    color: ColorKey;
    mandatory: boolean;
}

export const LABELS_DEFINITION: Record<LabelCategory, LabelDefinition[]> = {
    [LabelCategory.BASIC]: [],
    [LabelCategory.ESTIMATION]: [],
    [LabelCategory.EXECUTION]: [],
    [LabelCategory.PROJECT]: [],
    [LabelCategory.VALUE]: [],
    [LabelCategory.PRIORITIZATION]: []
}


const definition = (category: LabelCategory, {name, mandatory, color}: LabelDefinition) => {
    if (!LABEL_TYPES[category].match(name)) {
        throw new Error(`Wrong name: '${name}' for label with category: '${category}'`)
    }
    Object.keys(LABEL_TYPES).filter(type => type !== LabelCategory.BASIC && type !== category)
        .forEach(type => {
            if (type.match(name)) {
                throw new Error(`Wrong name: '${name}' for label with category: '${category}'. It matches to: '${type}'`)
            }})

    LABELS_DEFINITION[category].push({
        name,
        color,
        mandatory
    })
}


definition(LabelCategory.PROJECT, {name: "root", mandatory: true, color: ColorKey.GREY});
