import React from 'react';
import {TaskId} from "../../shared/TaskId";
import {useStoreActions} from "../../store";
import HotkeyConfirmation from "../../shared/hotkeys/HotkeyConfirmation";
import {ColorKey, getColorByKey} from "../../shared/Color";
import Icon from "../../shared/Icon";

interface DeleteTaskProps {
    taskId?: TaskId,
    disabled?: boolean;
}

function DeleteTask({taskId, disabled}: DeleteTaskProps) {
    const deleteTask = useStoreActions(actions => actions.deleteTask);


    const performDeleteTask = () => {
        if (taskId) {
            deleteTask({taskId});
        }
    }

    return <>
        <HotkeyConfirmation color={getColorByKey(ColorKey.RED)} disabled={disabled || !taskId} action={performDeleteTask}
                            theme="danger"
                            binding={"backspace"} label={<Icon name={"thrash.svg"}/>}/>
    </>;
}

export default DeleteTask;
