import React, {useState} from 'react';
import {useStoreActions, useStoreState} from "../store";
import {useNavigate} from "react-router-dom";
import {Hex} from "../shared/Color";
import ColorPicker from "../shared/components/color-picker/ColorPicker";
import styles from "./AddProfilePage.module.css"
import {nextProfileId} from "../todoist/auth/Profile";
import Icon from "../shared/Icon";

function AddProfilePage() {
    const profiles = useStoreState(state => state.profiles);
    let navigate = useNavigate();
    let addProfile = useStoreActions(actions => actions.addProfile);
    const [value, setValue] = useState("");
    const [name, setName] = useState("");
    const [color, setColor] = useState<Hex | undefined>(undefined)

    const handleSave = () => {
        if (value.length > 0 && name.length > 0) {
            const profileId = nextProfileId();
            addProfile({id: profileId, value, color, name});
            if (profiles.find(({id}) => id === profileId)?.active) {
                navigate("/");
            } else {
                navigate("/settings/profiles");
            }
        } else {
            alert("Form is not filled")
        }


    };

    return (
        <div>
            <h1>Add Profile</h1>
            <div className={styles.NameSection}>
                <ColorPicker color={color} onColorChange={setColor}/>
                <input
                    type="text"
                    placeholder="Name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                />
            </div>

            <br/>
            <input
                type="text"
                placeholder="API key"
                value={value}
                className={styles.InputSection}
                onChange={(e) => setValue(e.target.value)}
            />


            <div>
                <Icon size={32} name="floppy-disk.svg" onClick={handleSave}/>
            </div>
        </div>)
}

export default AddProfilePage;
