import {ApiKey} from "./ApiKey";
import {Hex} from "../../shared/Color";
import randomUUID from "../../shared/randomUUID";


export type ProfileId = `p-${string}-${string}-${string}-${string}-${string}`

export function nextProfileId(): ProfileId {
    return `p-${randomUUID()}`
}

export interface Profile {
    id: ProfileId,
    name: string,
    value: ApiKey,
    active: boolean,
    color?: Hex
}
