import styles from "./Progress.module.css"
import {ColorKey, getColorByKey, Hex} from "../shared/Color";
import ProgressItem from "../work/ProgressItem";
import {TaskId} from "../shared/TaskId";

export interface ProgressProps {
    taskIds: TaskId[];
    selectedId?: TaskId;
}

function color(value: number): Hex | undefined {

    if (value < 4) {
        return undefined
    }
    if (value < 8) {
        return getColorByKey(ColorKey.YELLOW)
    }
    if (value < 12) {
        return getColorByKey(ColorKey.ORANGE)
    }
    return getColorByKey(ColorKey.RED)
}

function Progress({taskIds, selectedId}: ProgressProps) {

    return <div className={styles.Progresses}>
        {taskIds.map((id, index) => (<ProgressItem key={id} active={id === selectedId} color={color(index)}/>))}
    </div>


}

export default Progress
